.profile-content-all-photos
{
  margin-bottom: 36px;
  &__item
  {
      padding-bottom:6px;
      padding-top: 7px;
      border-bottom: 1px solid #e6e6e6;
    

     
      ul
      {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
      
        li
        {
          margin-bottom: 12px;
          margin-left: 12px;
          &:hover
          {
            opacity: 0.8;

          }
          
          &:last-child
          {
            margin-left: 0;
          }
          a
          {
            width: 150px;
            height: 150px;
            display: flex;
            img
            {
              border-radius: 4px;
              width: 100%;
              height: 100%;
              object-fit: cover;
           
            }
          }
        }

      }
  }
  &__pagination
  {
    padding: 11px 0;
    border-bottom: 1px solid #e6e6e6;
    span
    {
      color: #333;
      font-size: 14px;

    }
  }
  &__modal
{
    &__content
    {
        padding: 12px 0 0 12px;
        background-color: #fff;
        &__title
        {
            margin-bottom: 6px;
            display: flex;
        
            &__avatar
            {
                margin-left: 10px;
                border-radius: 4px;
                flex:0 0 39px;
                max-width: 39px;
                a
                {
                    display: flex;
                    width: 39px;
                    height: 36px;
                    img
                    {
                        border-radius: 4px;
                        width: 100%;
                        height: 100%;

                    }
                }
            }
            &__info
            {
                flex:0 0 calc(100% - 49px) ;
            max-width: calc(100% - 49px);
                &__name
                {
                
                    a
                    {
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;
                        display: block;
                        color: #0073bb;
                        font-size: 14px;
                        font-weight: 500;
                        &:hover
                        {
                            text-decoration: underline;
                        }

                    }
                    
                }
                &__status
                {
                    ul
                    {
                        display: flex;
                        align-items: center;
                        li
                        {
                            margin-left: 6px;
                            &:last-child
                            {
                                margin-left: 0;
                            }
                            svg
                            {
                                fill: rgb(241, 92, 0);
                            width: 18px;
                            height: 18px;
                            }
                            span
                            {
                                color: #666;
                                font-weight: 500;
                            }
                            a
                            {
                                color: #f15c00;
                                font-weight: 500;
                                &:hover
                                {
                                    text-decoration: underline;
                                }

                            }

                        }
                    }
                }
            }
        }
        &__desc
        {
            p
            {
                color: #333;
                margin-bottom: 6px;

            }
        }
        &__date
        {
            span
            {
                color: #999;
                font-size: 12px;

            }
        }
    }
    &__close
    {
        position: absolute;
        left: 10px;
        top:-25px;
        svg
        {
            fill:#999;;

        }
        span
        {
            color: #999;
        }
        &:hover
        {
            svg
            {
                fill:#000;;
    
            }
            span
            {
                color: #000;
            }
        }
    } 
    &__image
    {
    
        background-color: #000;
        display: flex;
        justify-content: center;
        text-align: center;
        align-items: center;
        height: 100%;
        a
        {
            img
            {

            }
        }
    }
    &__footer-slide
    {
        background: rgba(0,0,0,.7);
        height: 30px;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        padding: 2px 0;
        &__left
        {
            margin-right: auto;
            margin-left: 6px;
            a
            {
                color: #999;
                svg
                {
                    margin-left: 3px;
                    fill:#999;;

                }
            
                &:hover
                {
                    color: #fff;
                    svg
                    {
                        fill: #fff;
                    }
                
                }
            }
        }
        &__right
        {
            margin-right: 6px;
            a
            {
                color: #999;
                svg
                {
                    margin-left: 3px;
                    fill:#999;

                }
            
                &:hover
                {
                    color: #fff;
                    svg
                    {
                        fill: #fff;
                    }
                
                }
            }
        }
    }
    .modal-content
    {
        border: none;
        border-radius: 6px;
    }
    .modal-content
    {
        border: none;
        border-radius: 6px;
    }
    .modal-body
    {
        padding: 0;
        border-radius: 6px;
    }
    .modal-dialog 
    {
        max-width:1300px;
        padding: 0 15px;
    }
    .swiper-container
    {
        height: 90vh;
    }

    .swiper-button-next
    {
        &::after
        {
            width: 48px;
            height: 48px;
            color: #fff;
        }
    }
    .swiper-button-prev
    {
        &::after
        {
            width: 48px;
            height: 48px;
            color: #fff;
        }
    }
    .swiper-pagination-fraction
    {
        color: #999;
        bottom: 3px;
    }
}
}

@media(max-width:576px)
{
  .profile-content-all-photos
  {
    &__modal
    {
      margin-top: 30px;
      &__content
      {
        padding:12px;
      }
      .swiper-container
    {
        height: 70vh;
    }
      
    }
    
  }
 
}



@media(max-width:480px)
{
  .profile-content-all-photos
  {
    &__item
    {
      ul
      {
        li
        {
          height: 180px;
          flex: 0 0 calc(50% - 12px);
          a
          {
            width: 100%;
            height: 100%;
        
            img
            {
              width: 100%;
              height: 100%;
             
            }
          }
        }
      }
    }
  }
}