.stay-gold-content {
	margin-bottom: 32px;
	
	.container {
		max-width: 1144px;
		margin: 0 auto;
	}
	.swiper

	{
		&-btn
		{
			
			&-next
			{
				box-shadow: 0 0 10px rgba(0,0,0,.1);
				background: #fff;
				border: 1px solid #eeeeef;
				border-radius: 50%;
				height: 24px;
				width: 24px;
				top: 35%;
				right:10px;
				position: absolute;
				z-index: 2;
				text-align: center;
				display: flex;
				align-items: center;
				opacity: .8;
				svg
				{
	

				}
			}
		}
	}

	&__wrapper
	{
		border-bottom: 1px solid #eeeeef;

	
		&__covid-19 {
			&__title {
				margin-bottom: 32px;
	
				h4 {
					color: rgba(43, 39, 60, 1);
					font-weight: 500;
					font-size: 20px;
					line-height: 26px;
				}
			}
	
			&__update {
	
				&__title {
					margin-bottom: 16px;
	
					span {
						color: rgba(43, 39, 60, 1);
						font-weight: 500;
						font-size: 16px;
						line-height: 22px;
					}
				}
	
				&__list {
					margin-bottom: 16px;
	
					ul {
						display: flex;
						align-items: center;
						flex-wrap: wrap;
	
						li {
							font-size: 16px;
							line-height: 22px;
							font-weight: 400;
							color: rgb(76, 69, 107);
							margin-left: 24px;
							margin-bottom: 8px;
	
							svg {
								margin-left: 8px;
	
								&.icon_svg-green {
									fill: rgba(5, 168, 130, 1);
	
								}
	
								&.icon_svg-red {
									fill: rgba(255, 82, 61, 1);
	
								}
	
							}
						}
					}
				}
			}
	
			&__health {
				&__title {
					margin-bottom: 16px;
					position: relative;
					transition:0.5s;

					$title:&;
					&__popover
					{
						visibility: hidden;
						opacity: 0;
						position: absolute;
					   bottom: 35px;
						right:165px;
						 width: 300px;
						 transition: 0.5s;
						 background: #2b273c;
						 border-color: #2b273c;
						 border-radius: 4px;
						 padding: 12px;
						 transition:0.5s;
						 &:hover
						 {
							 opacity: 1;
							 visibility: visible;

						 }

						
						&:before
						{
							position: absolute;
							content: '';
							border-width: 10px 10px 0 10px;
							border-style: solid;
							border-color: #2b273c transparent transparent;
							bottom: -10px;
							right:50%;
						}
						&:after
						{
							content: '';
							border-width: 9px 9px 0 9px;
							border-style: solid;
							border-color: #2b273c transparent transparent;
							position: absolute;
							bottom: -8px;
							right:50%;
						}
						>p
						 {
							color: #fff;
							font-size: 12px;
							line-height: 20px;
							margin-bottom: 30px;
						 }
						 >a
						 {
							 font-size: 14px;
							 line-height: 20px;
							 color: #0ab3c9;
							 &:hover
							 {
								 text-decoration: underline;
							 }
	 
						 }
					}
					span {
						color: rgba(43, 39, 60, 1);
						font-weight: 500;
						font-size: 16px;
						line-height: 22px;
						display: flex;
						align-items: center;
						>p
						{
							margin-right: 12px;
							font-size: 14px;
							line-height: 20px;
							font-weight:500;
							color: rgba(117,114,128,1);
							display: flex;
							align-items: center;
					        transition:0.5s;

							&:hover
							{
								
								+#{$title}__popover
								{
										visibility:visible;
									opacity: 1;
								}
							}
							svg
							{
								margin-right: 6px;
								fill: rgba(187,186,192,1);

							}
						}
						
					}
				}
	
				&__list {
					margin-bottom: 16px;
	
					ul {
						display: flex;
						align-items: center;
						flex-wrap: wrap;
	
						li {
							font-size: 16px;
							line-height: 22px;
							font-weight: 400;
							color: rgb(76, 69, 107);
							margin-left: 24px;
							margin-bottom: 8px;
	
							svg {
								margin-left: 8px;
	
								&.icon_svg-green {
									fill: rgba(5, 168, 130, 1);
	
								}
	
								&.icon_svg-red {
									fill: rgba(255, 82, 61, 1);
	
								}
	
							}
						}
					}
				}
			}
	
	
		}
	
