					}
				}
				&__health
				{
					&__title
					{
						span
						{
							flex-direction: column;
							align-items: flex-start;
							p
							{
								margin-right: 0;
							}
						}
					}

					&__list
					{
						ul
						{
							flex-direction: column;
							align-items: start;
							li
							{
								font-size: 14px;

							}
						}
					}
				}
			}
			&__location
			{
				&__content
				{
					&__right
					{
						width: 100%;
						&__map-location
						{
							width: 100%;
						}
						&__text
						{
							flex-direction: column;
							align-items: start;
							&__left
							{
								margin-right: 0;
								margin-top: 12px;
							}
						}
					}
					&__left
					{
						margin-top:24px;
						ul
						{
							li
							{
								font-size: 12px;
								span
								{
									margin-left:4px;
									font-size: 12px;
								}
								p
								{
									font-size: 11px;
								}
							}
						}
					}
				}
			}
			&__consider
			{
				&__title
				{
					flex-direction: column;
					align-items: flex-start;
					h4
					{
						font-size: 15px;
						margin-left: 0;
					}
					span
					{
						font-size: 16px;
					}
				}
				&__content
				{
					flex-direction: column;
					&__image
					{
						flex: 0 0 100%;
						max-width: 100%;
						margin-left: 0;
					}
					&__text
					{
						flex: 0 0 100%;
						max-width: 100%;
						&__favorite
						{
							flex-direction: column;
							align-items: start;
							&__location
							{
								margin-right: 0;
							}
						}

					}
				}
			}
			&__ask
			{
				&__title
				{
					h4
					{
						font-size: 15px;
					}
					a
					{
						font-size: 12px;
					}
				}
				&__desc
				{
					&__sub
					{
						span
						{
							flex-direction: column;
    						align-items: flex-start;
						}
					}
				}
			}
			&__recommended
			{
				&__alert
				{
					position: relative;
					padding-top: 30px;
				
					p
					{
						width: calc(100% - 40px);
						font-size: 12px;
						span
						{
							font-size: 12px;
						}

					}
					&__close
					{
						position: absolute;
						top: 5px;
						left: 5px;
					}


				}
				&__score
				{
					flex-direction: column;
					&__avator
					{
						margin-bottom: 12px;
						flex: 0 0 100%;
						max-width: 100%;
					}
					&__star
					{
						flex: 0 0 100%;
						max-width: 100%;
						align-items: center;
						display: flex;
						flex-direction: column;
						&__text
						{
							text-align: center;
						}
					}
				}
			}
			&__comment
			{
				&__image
				{
					&__item
					{
						a
						{
							height: unset;
							span
							{
							font-size: 10px;

							}
   						}
					}
				}
				&__see-all
				{
					a
					{
						font-size: 12px;
					}
				}
				&__actions
				{
					ul
					{
						li
						{
							a
							{
								padding: 0 6px;
							}
						}

					}
				}
			}
			&__item
			{
				&__sidebar
				{
					&__title
					{
						&__name
						{
							h4
							{
								font-size: 15px;
							}
						}
					}
					&__list
					{
						&__avatar
						{
							&__bio
							{
								&__name
								{
									a
									{
										font-size: 15px;
									}

								}
							}
						}
					}
				}
			}
		}
		&__modal
		{
			.modal-dialog
			{
				max-width:100%;
			}
			
			&__body
			{
				height:unset;
				&__content
				{
					&__desc
					{
						
						p
						{
							font-size: 12px;
						}
					}
		
				}
	
			}
		}
	}
}