.stay-gold-footer
{
 
    .container {
        max-width: 1144px;
        margin: 0 auto;
    }
   &__wrapper
   {
    border-bottom: 1px solid #eeeeef;
    margin-bottom:48px;
   &__content
   {
    &__item
    {
        margin-bottom: 32px;
        &__title
        {
            h4
            {
                color: rgb(43, 39, 60);
                font-weight:500;
                font-size: 20px;
                line-height: 26px;

            }

        }
        &__list
        {
            ul
            {
                li
                {
                    padding: 8px 0;
                    a
                    {
                        svg
                        {
                            fill: rgb(102, 102, 102);
                            margin-left:12px;
                        }
                        font-size: 14px;
                        line-height: 20px;
                        color: rgba(117,114,128,1);
                        &:hover{
                            text-decoration: underline;
                        }

                    }
                }
            }
        }
    }
   }
   }
}
@media(max-width:768px)
{
    .stay-gold-footer
    {
        &__wrapper
        {
           &__content
           {
               display: flex;
               justify-content: space-between;
           }
        }
    }
}
@media(max-width:480px)
{
    .stay-gold-footer
    {
        &__wrapper
        {
           &__content
           {
              
            flex-direction: column;
           }
        }
    }
}