.collection-content {
    position: fixed;
    top: 153px;
    bottom: 0;
    left: 0;
    right: 0;

    &__wrapper {
        display: flex;
        height: 100%;
        &__right {
            flex: 0 0 720px;
            width: 720px;
            overflow: scroll;
            height: 100%;
            &__gallery {
                &__item {
                    width: 100%;
                    height: 316px;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                    &--small {
                        height: 158px;
                    }
                    &--more {
                        background-color: rgba(0, 0, 0, 0.3);
                    }
                    &__content {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        height: 100%;
                        span {
                            color: #fff;
                            font-size: 14px;
                            font-weight: 500;
                            margin-right: 6px;
                            svg {
                                fill: #fff;
                            }
                        }
                    }
                }
            }
            &__info {
                margin: 36px 36px 0 36px;
                border-bottom: 1px solid #e6e6e6;

                &__header {
                    display: flex;
                    align-items: center;
                    margin-bottom: 18px;
                    &__fllow {
                        span {
                            color: #333;
                            font-size: 14px;
                        }
                    }
                    &__date {
                        margin-right: auto;
                        span {
                            color: #999;
                            font-size: 14px;
                        }
                    }
                }
                &__title {
                    margin-bottom: 18px;

                    h1 {
                        font-size: 30px;
                        font-weight: 500;
                        color: #333;
                    }
                }
                &__avatar {
                    display: flex;
                    align-items: center;
                    margin-bottom: 36px;
                    &__image {
                        height: 30px;
                        width: 30px;
                        margin-left: 6px;

                        a {
                            width: 100%;
                            height: 100%;
                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                border-radius: 4px;
                            }
                        }
                    }
                    &__bio {
                        &__name {
                            a {
                                color: #0073bb;
                                font-size: 14px;
                                font-weight: 500;
                                &:hover {
                                    text-decoration: underline;
                                }
                            }
                        }
                        &__status {
                            ul {
                                display: flex;
                                align-items: center;
                                li {
                                    margin-left: 6px;
                                    svg {
                                        fill: #f15c00;
                                    }
                                }
                            }
                        }
                    }
                }
                &__place {
                    margin-bottom: 6px;
                    h3 {
                        color: #333;
                        font-size: 16px;
                        font-weight: 500;
                    }
                }
                &__sort {
                    display: flex;
                    align-items: center;
                    margin-bottom: 3px;
                    :not(.input-group) > .bootstrap-select.form-control:not([class*=col-])
                    {
                        width: unset;
                    }
                    .btn:not(:disabled):not(.disabled)
                    {
                        background-color: transparent;
                        border: none;
                        color: #666;
                        font-size: 12px;
                    }
                    .btn-light.focus, .btn-light:focus
                    {
                        box-shadow: none;
                    }
                    .dropdown-item:focus, .dropdown-item:hover
                    {
                        background-color: #c7c3c3;
                        color: #fff;
                    }
                 
                    .selected 
                    {
                        background-color: #c7c3c3;
                        color: #fff;
                    }
                    .filter-option-inner-inner
                    {
                        padding-left: 4px;
                    }
               

                    &__recently {
                        margin-left: 12px;
                        display: flex;
                       align-items: center;
                        span {
                            color: #666;
                            font-size: 12px;
                            &:hover
                            {
                                color: #fff;
                            }
                        }
                        .btn:not(:disabled):not(.disabled)
                        {
                         
                            font-size: 14px;
                        }
                    
                    }
                    &__all {
                        margin-left: 12px;
                        span {
                            color: #666;
                            font-size: 12px;
                            &:hover
                            {
                                color: #fff;
                            }
                        }
                        
                    }
                    &__all-city {
                        span {
                            color: #666;
                            font-size: 12px;
                            &:hover
                            {
                                color: #fff;
                            }
                        }
                    }
                }
            }
            &__list {
                display: flex;
                margin: 0 36px;

                > ul {
                    width: 100%;
                    > li {
                        display: flex;
                        align-items: center;
                        border-bottom: 1px solid #e6e6e6;
                        width: 100%;
                        padding: 18px 0 36px 0;
                        flex-wrap: wrap;
                        &:last-child {
                            border-bottom: none;
                        }
                    }
                }
                &__item {
                    display: flex;
                    &__image {
                        width: 90px;
                        height: 90px;
                        margin-left: 12px;
                        a {
                            width: 100%;
                            height: 100%;
                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                border-radius: 4px;
                            }
                        }
                    }
                    &__text {
                        &__title {
                            margin-bottom: 6px;
                            a {
                                color: #0073bb;
                                font-weight: 500;
                                font-size: 14px;
                            }
                        }
                        &__star {
                            display: flex;
                            align-items: center;
                            margin-bottom: 6px;
                            &__icon-star {
                                margin-left: 8px;
                                width: 108px;
                                height: 20px;
                                background-image: url(../img/star.png);
                                background-repeat: no-repeat;

                                &[data-star="1"] {
                                    background-position: 0 -320px;
                                }

                                &[data-star="1"] {
                                    background-position: 0 -360px;
                                }

                                &[data-star="1.5"] {
                                    background-position: 0 -340px;
                                }

                                &[data-star="2"] {
                                    background-position: 0 -400px;
                                }

                                &[data-star="2.5"] {
                                    background-position: 0 -380px;
                                }

                                &[data-star="3"] {
                                    background-position: 0 -440px;
                                }

                                &[data-star="3.5"] {
                                    background-position: 0 -420px;
                                }

                                &[data-star="4"] {
                                    background-position: 0 -480px;
                                }

                                &[data-star="4.5"] {
                                    background-position: 0 -460px;
                                }

                                &[data-star="5"] {
                                    background-position: 0 -500px;
                                }
                            }
                            &__review {
                                margin-right: 6px;
                                span {
                                    color: #666;
                                    font-weight: 400;
                                    font-size: 14px;
                                    line-height: 20px;
                                }
                            }
                        }
                        &__category {
                            ul {
                                display: flex;
                                align-items: center;
                                li {
                                    margin-left: 6px;
                                    color: #333;
                                    font-size: 12px;
                                    a {
                                        color: #0073bb;
                                        &:hover {
                                            text-decoration: underline;
                                        }
                                    }
                                }
                            }
                        }
                        &__address {
                            span {
                                color: #333;
                                font-size: 12px;
                            }
                        }
                    }
                }
                &__save {
                    display: flex;
                    margin-right: auto;
                    svg {
                        fill: #666;
                        &:hover {
                            fill: #d32323;
                        }
                    }
                }
                &__test {
                    flex: 0 0 100%;
                    width: 100%;
                    margin: 18px 0;
                    span {
                        color: #333;
                    }
                }
                &__slider {
                    display: none;
                    &__image {
                        a 
                        {
                            img 
                            {
                                border-radius: 4px;
                            }
                        }
                    }
                }
            }
        }
        &__left {
            flex: 0 0 calc(100% - 720px);
            width: calc(100% - 720px);
            &__map {
                height: 100%;
            }
        }
    }
}

@media (max-width: 992px) {
    .collection-content {
        position: static;
        &__wrapper {
            flex-direction: column;
            &__right {
                flex: 0 0 100%;
                width: 100%;
                overflow: visible;
            }
            &__left {
                flex: 0 0 100%;
                width: 100%;
                &__map
                {
                    height: 500px;
                }
            }
        }
    }
}

@media (max-width: 749px) {
    .collection-content {
        &__wrapper {
            &__right {
                &__gallery {
                    margin-top: 98px;
                }
            }
        }
    }
}

@media (max-width: 576px) {
    .collection-content {
        &__wrapper {
            &__right {
                &__gallery {
                    &__item {
                        &--small {
                            display: none;
                        }
                    }
                }
                &__info
                {
                    &__sort {
                        display: none;
                    }
                }
            }
            &__left
            {
                display: none;
            }
        }
    }
}

@media (max-width: 480px) {
    .collection-content {
        &__wrapper {
            &__right {
                &__gallery {
                    &__item {
                        height: 150px;
                    }
                }
                &__info {
                    margin:12px 12px 0 12px;
                
                    &__header
                    {
                        display: none;
                    }
                }
                &__list
                {
                    margin: 0 12px;
                    ul
                    {
                        li
                        {
                            position: relative;
                            padding: 12px 0 12px 0;
                            display: block;
                        }
                    }
                    &__save
                    {
                        position: absolute;
                        top: 10px;
                        left: 0;
                    }
                    &__item
                    {
                        &__image
                        {
                            display: none;
                        }
                    }
                    &__slider
                    {
                        display: flex;
                        margin-top: 12px;
                    }
                }
            }
        }
    }
}
