.profile-content-photos
{     
    margin-bottom: 36px;
 
      &__item
      {
          position: relative;
     
        &__image
        {
        
            background-color: #000;
            display: flex;
            justify-content: center;
            text-align: center;
            align-items: center;
            height: 440px;
            a
            {
                img
                {

                }
            }
        }
        &__footer-slide
        {
            z-index: 999;
            height: 30px;
            position: absolute;
            bottom: 0px;
            left: 0;
            right: 0;
            display: flex;
            padding: 2px 0;
            &__left
            {
                margin-right: auto;
                margin-left: 6px;
                a
                {
                    color: #999;
                    svg
                    {
                        margin-left: 3px;
                        fill:#999;;

                    }
                
                    &:hover
                    {
                        color: #fff;
                        svg
                        {
                            fill: #fff;
                        }
                    
                    }
                }
            }
            &__right
            {
                margin-right: 6px;
                a
                {
                    color: #999;
                    svg
                    {
                        margin-left: 3px;
                        fill:#999;

                    }
                
                    &:hover
                    {
                        color: #fff;
                        svg
                        {
                            fill: #fff;
                        }
                    
                    }
                }
            }
           
        }
      }
      &__content
      {
          padding: 12px 0 0 12px;
          background-color: #fff;
          &__title
          {
              margin-bottom: 6px;
              display: flex;
          
              &__avatar
              {
                  margin-left: 10px;
                  border-radius: 4px;
                  flex:0 0 39px;
                  max-width: 39px;
                  a
                  {
                      display: flex;
                      width: 39px;
                      height: 36px;
                      img
                      {
                          border-radius: 4px;
                          width: 100%;
                          height: 100%;

                      }
                  }
              }
              &__info
              {
                  flex:0 0 calc(100% - 49px) ;
              max-width: calc(100% - 49px);
                  &__name
                  {
                  
                      a
                      {
                          text-overflow: ellipsis;
                          overflow: hidden;
                          white-space: nowrap;
                          display: block;
                          color: #0073bb;
                          font-size: 14px;
                          font-weight: 500;
                          &:hover
                          {
                              text-decoration: underline;
                          }

                      }
                      
                  }
                  &__status
                  {
                      ul
                      {
                          display: flex;
                          align-items: center;
                          li
                          {
                              margin-left: 6px;
                              &:last-child
                              {
                                  margin-left: 0;
                              }
                              svg
                              {
                                  fill: rgb(241, 92, 0);
                              width: 18px;
                              height: 18px;
                              }
                              span
                              {
                                  color: #666;
                                  font-weight: 500;
                              }
                              a
                              {
                                  color: #f15c00;
                                  font-weight: 500;
                                  &:hover
                                  {
                                      text-decoration: underline;
                                  }

                              }

                          }
                      }
                  }
              }
          }
          &__desc
          {
              p
              {
                  color: #333;
                  margin-bottom: 6px;

              }
          }
          &__date
          {
              span
              {
                  color: #999;
                  font-size: 12px;

              }
          }
      }
        .swiper-container
        {
            // height: 90vh;
        }
    
        .swiper-button-next
        {
            &::after
            {
                width: 48px;
                height: 48px;
                color: #fff;
            }
        }
        .swiper-button-prev
        {
            &::after
            {
                width: 48px;
                height: 48px;
                color: #fff;
            }
        }
        .swiper-pagination-fraction
        {
            color: #999;
            bottom:10px;
        }
    
}

@media(max-width:768px)
{
    .profile-content-photos
    {
        &__item
        {
            &__image
            {
                margin-bottom: 10px;
            }
            &__footer-slide
            {
                bottom: 10px;
            }
            .swiper-pagination-fraction
            {
                bottom: 20px;
            }
        }
    }
}