.review-content
{
    display: flex;
    padding-left:341px ;
    min-height: 100vh;
    position: relative;
    transition: 0.8s;
    overflow-x: hidden;
    $review-content:&;
    
    &__arrow
    {
        width: 20px;
        height: 40px;
        position: absolute;
        top: 10px;
        left:341px;
        align-items: center;
        text-align: center;
        display: flex;
        border:1px solid #eeeeef;
        border-left: none;
        transition: all 0.8s;
        background:#fff;
        z-index: 999;

        
        svg
        {
            fill: rgba(43,39,60,1);
            transform: rotate(180deg);
        }
        &:hover
        {
            background-color: rgba(0,0,0,0.12);
            background-image: radial-gradient(rgba(0,0,0,0.05) 1%,transparent 1%);
        }
    }
    &__wrapper
    {
       
        display: flex;
        flex-direction: column;
        text-align: center;
        position: relative;
       width: 625px;
       max-width: 100%;
        margin: 0 auto;
        padding: 0 15px;
       
        &__title
        {
            margin-top: 36px;
            margin-bottom: 24px;
            display: flex;
            align-items: center;


            h2
            {
                color: rgba(43,39,60,1);
                font-size: 28px;
                line-height: 36px;
            }
            a
            {
                display: flex;
                margin-right: auto;
                color: rgba(0,131,143,1);
                font-size: 14px;
                line-height: 20px;
                &:hover
                {
                    text-decoration:underline;
                }
            }
        }
        &__box-comment
        {
            margin-bottom: 48px;
            padding: 24px;
            border: 1px solid #bbbac0;
            border-radius: 4px;
           
            &__score
            {
                display: flex;
                margin-bottom: 24px;
                align-items: center;
                &__star
                {
                    display: flex;
					align-items: center;
                  
					input {
						display: none;
                        &:checked
                        {
                            +label
                            {
                                &[data-star="1"] {
                                    background-position: 0 -64px;
                                    ~label {
                                        background-position: 0 -64px;
                                    }
                                }
        
                                &[data-star="2"] {
                                    background-position: 0 -128px;
                                    ~label {
                                        background-position: 0 -128px;
                                    }
                                }
        
                                &[data-star="3"] {
                                    background-position: 0 -192px;
                                    ~label {
                                        background-position: 0 -192px;
                                    }
                                }
        
                                &[data-star="4"] {
                                    background-position: 0 -256px;
                                    ~label {
                                        background-position: 0 -256px;
                                    }
                                }
        
                                &[data-star="5"] {
                                    background-position: 0 -288px;
                                    ~label {
                                        background-position: 0 -288px;
                                    }
                                }
                            }
                        }
					}

					label {
						width: 32px;
						height: 32px;
						background-position: 0 0;
						background-image: url(../img/star.png);
						background-repeat: no-repeat;
						margin-left: 2px;
						cursor: pointer;
                        margin-bottom: 0;


						&[data-star="1"] {
							&:hover {
								background-position: 0 -64px;

								~label {
									background-position: 0 -64px;
								}
							}
						}

						&[data-star="2"] {
							&:hover {
								background-position: 0 -128px;

								~label {
									background-position: 0 -128px;
								}
							}
						}

						&[data-star="3"] {
							&:hover {
								background-position: 0 -192px;

								~label {
									background-position: 0 -192px;
								}
							}
						}

						&[data-star="4"] {
							&:hover {
								background-position: 0 -256px;

								~label {
									background-position: 0 -256px;
								}
							}
						}

						&[data-star="5"] {
							&:hover {
								background-position: 0 -288px;

								~label {
									background-position: 0 -288px;
								}
							}
						}
					}
                    
                }
                &__text
                {
                    margin-right:8px;
               
                    span
                    {
                        color: #757280;
                        font-size: 14px;
                        line-height: 20px;
                       
                    }
                }
                &__save
                {
                    display: flex;
                    margin-right: auto;
                    span
                    {
                        font-size: 14px;
                        line-height: 20px;
                        color: #bbbac0;

                    }
                }
            }
            &__desc
            {
                width: 100%;
                textarea
                {
                    min-height: 394px;
                    width: 100%;
                    border: none;
                    color: #000;
                    font-size: 16px;
                    resize: none;
                }
               
            }
          
        }
        &__attach
        {
            
            $attach: &;
            &__title
            {
                margin-bottom: 16px;
                h4
                {
                    color: rgba(43,39,60,1);
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 26px;
                }
            }
            &__box
            {
                margin-bottom: 48px;
                border: 1px solid #ccc;
                border-radius: 4px;
                height: 110px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                &__icon-add
                {
                    svg
                    {
                        fill: #999;

                    }
                }
                &:hover
                {
                    cursor: pointer;
                    border-color: #999;
                    border-width: 2px;

                }
            }
            &__box-list
            {
                display: flex;
                flex-wrap: wrap;
                margin: 0 -5px;
                #{$attach}__box
                {
                    flex: 0 0 110px;
                    max-width: 110px;
                    margin: 0 5px;
                    margin-bottom: 15px;
                }
                &__image
                {
                    flex: 0 0 110px;
                    max-width: 110px;
                    height: 110px;
                    margin: 0 5px;
                    margin-bottom: 15px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: relative;
                    border-radius: 4px;
                    overflow: hidden;
                    img
                    {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                    &:before
                    {
                        content: '';
                        @include absolute(40px,0,0,0);
                        border-radius: 4px;
                        background-image: linear-gradient(to top, rgba(0,0,0,0.8),rgba(0,0,0,0));
                        z-index: 2;
                    }
                    a
                    {
                        @include absolute(10px,10px,f,f);
                        background-color: rgba(0,0,0,0.6);
                        border-radius: 2px;
                        padding: 2px 5px;
                        opacity: 0;
                        transition: 0.3s;
                        svg
                        {
                            fill: rgba(255,255,255,0.9);
                        }
                    }
                    &:hover
                    {
                        a
                        {
                            opacity: 1;
                        }
                    }
                }
            }
        }
        &__button
        {
            display: flex;
         
            
            a
            {
                display: flex;
                background-color: rgba(244,57,57,1);
                color: rgba(255,255,255,1);
                transition: all 0.8s;
                line-height: 40px;
                padding: 0 16px;
                border-radius: 4px;
                max-width: 200px;
                text-align: center;
                justify-content: center;
                align-items: center;
                font-weight: 500;
                font-size: 16px;
                margin-bottom: 48px;

                &:hover
                {
                    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.3);
                    background-color: rgb(244,13,21);
                    
                }


            }
        }
    }
    &__recent
    {
        position: absolute;
        left: 0;
        bottom: 0;
        top: 0;
        flex: 0 0 341px;
        width: 341px;
        border-right: 1px solid #eeeeef;
        padding:0 20px;
        overflow-y: scroll;
        transition: 0.8s;

      
        &__title
        {
            padding-top: 16px;
            h4
            {
                color: rgba(43,39,60,1);
                font-weight: bold;
                font-size: 20px;
                line-height: 26px;
            }
        }
        &__comment
        {
            padding-top: 24px;
            border-bottom: 1px solid ;
            border-color: #eeeeef;
            &__avatar
            {
                display: flex;
                margin-bottom: 24px;

                // align-items: center;
                &__image
                {
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;

                    
                    a
                    {
                        width: 100%;
                        height: 100%;
                        img
                        {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            border-radius: 50%;

                        }
                    }
                }
                &__text
                {
                    margin-right:8px;
                    &__name
                    {
                        display: flex;
                        align-items: center;
                      
                        span
                        {
                            font-size: 16px;
                            line-height: 22px;
                            font-weight:500;
                            color: rgba(43,39,60,1);

                        }
                        p
                        {
                            margin-right: 4px;
                            background-color: #f43939;
                            padding: 4px;
                            border-radius: 4px;
                            color: #ffffff;
                            line-height:8px;
                            cursor: pointer;
                        }
                    }
                    &__actions
                    {
                        ul
                        {
                            display: flex;
                            align-items: center;
                            li
                            {
                                margin-right: 8px;
                                display: flex;
                                align-items: center;
                                font-size: 14px;
                                line-height: 20px;
                                font-weight: 600;
                                color: rgba(117,114,128,1);
                                svg
                                {
                                    fill: rgba(117,114,128,1);
                                    margin-left: 4px;
                                }
                                &:first-child
                                {
                                    margin-right: 0;
                                }

                            }
                        }
                    }
                }
            }
            &__desc
            {
                margin-top: 8px;
                margin-bottom: 16px;
                p
                {
                    font-size: 14px;
                    line-height: 20px;
                    color: rgba(43,39,60,1);
                }
            }
            &__more
            {
                p
                {
                    font-size: 14px;
                    line-height: 20px;
                    color: rgba(43,39,60,1);
                }
            }
            &__read-more
            {
                margin-bottom: 24px;
                a
                {
                    color: rgba(0,131,143,1);
                    font-size: 14px;
                    line-height: 20px;
                    font-weight: 500;
                    &:hover
                    {
                        text-decoration: underline;

                    }
                }
            }
        }
    }
    &__modal
    {
        .modal-dialog 
        {
            max-width: 960px;
        }
        &__content
        {
            padding: 30px ;
            &__image
            {
                text-align: center;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-bottom: 10px;
                img
                {

                }
            }
           
            &__close
            {
                margin-bottom: 12px;
                display: flex;
                flex-direction: row-reverse;
                a
                {
                    margin-right: auto;
                    svg
                    {
                        fill: rgb(187, 186, 192);
                    }
                }
                h3
                {
                    font-size: 16px;
                    display: none;
                }
            }
            .dropzone
            {
                border:2px dashed #ccc;
                border-radius: 8px;
                padding: 0;
                form
                {
                    padding: 50px 15px;
                    cursor: pointer;
                }
                &.loaded
                {
                    border: none;
                    form
                    {
                        padding: 10px;
                    }
                }
                
                .dz-message
                {
                    display: flex;
                    justify-content: center;
                    flex-direction: column;
                    align-items: center;
                    >span
                    {
                        color: rgb(43, 39, 60);
                        font-weight:500;
                        font-size: 24px;
                        line-height: 30px;
                        margin-bottom: 6px;
                    }
                    .hr-line
                    {
                        position: relative;
                        margin-bottom: 15px;
                        font-size: 12px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 700px;
                        max-width: 100%;
                        span
                        {
                            position: relative;
                            z-index: 2;
                            padding: 0 15px;
                            background-color: #fff;
                            font-size: 14px;
                        }
                       
                    }
                    button
                    {
                        color: #fff;
                        background-color: rgb(244, 57, 57);
                        padding: 0 35px;
                        line-height: 36px;
                        border: none;
                        border-radius: 3px;
                        font-size: 13px;
                        font-weight: 600;
                        display: inline-block;
                        transition: 0.8s;
                        &:hover
                        {
                            background-color: rgb(244, 13, 21);

                        }
                    }
                }
                .dz-image
                {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: unset;
                }
                .dz-preview
                {
                    label
                    {
                        display: block;
                        margin-bottom: 10px;
                    }
                    textarea
                    {
                        width: 100%;
                        height: 90px;
                        border: 1px solid #ddd;
                        border-radius: 4px;
                        padding: 10px;
                        background-color: transparent;
                    }
                }
            }
            &__footer
            {
                display: flex;
                justify-content: flex-end;
                margin-top: 15px;
                display: none;
                button
                {
                    background-color: #f43939;
                    color: white;
                    transition: all 0.8s;
                    line-height: 40px;
                    padding: 0 16px;
                    border-radius: 4px;
                    text-align: center;
                    align-items: center;
                    font-weight: 500;
                    font-size: 16px;
                    border: none;
                    
                }
                a
                {
                    display: inline-block;
                    background-color: #fff;
                    color: #222;
                    transition: all 0.8s;
                    line-height: 38px;
                    padding: 0 16px;
                    border-radius: 4px;
                    text-align: center;
                    align-items: center;
                    font-weight: 500;
                    font-size: 16px;
                    border: 1px solid #ddd;
                    margin-left: 15px;
                }
            }
        }

   
    }

    &--close-sidebar
    {
        padding-left: 0;
        #{$review-content}__recent
        {
            left: -341px;

        }
        #{$review-content}__arrow
        {
            left: 0; 
            svg
            {
                transform: rotate(0);
            }
        }


    }
}

@media(max-width:992px)
{
    .review-content
    {
        &__wrapper
        {
            &__box-comment
            {
                &__score
                {
                    flex-direction: column;
                    justify-content: flex-start;
                    &__save
                    {
                        margin-right: 0;
                    }
                    &__text
                    {
                        text-align: right;
                        margin-right: 0;
                    }
                }
            }
        }
    }
}
@media(max-width:768px)
{
    .review-content
    {
        $review-content:&;
        padding-left: 0;
        &__wrapper
        {
            width: 100%;
            &__box-comment
            {
                &__score
                {
                    flex-direction:row;
                    justify-content:center;
                    &__save
                    {
                        margin-right: auto;
                    }
                    &__text
                    {
                        text-align: right;
                        margin-right:8px;
                    }
                }
            }
        
        }
        
        &__recent
        {
            left: -341px;
            background:#fff;
            z-index: 99;


        }
        &__arrow
        {
            left: 0; 
    
        }
        &--close-sidebar
    {
        padding-left: 0;
        #{$review-content}__recent
        {
            left:0;

        }
        #{$review-content}__arrow
        {
            left:341px; 
         
        }
    


    }
    
    }
   
}
@media(max-width:576px)
{
    .review-content
    {
        &__wrapper
        {
            &__title
            {
                flex-direction: column;
                align-items: flex-start;
                a
                {
                    margin-right: 0;
                }
            }
            &__box-comment
            {
                &__score
                {
                    flex-direction: column;
                    justify-content: flex-start;
                    &__save
                    {
                        margin-right: 0;
                    }
                    &__text
                    {
                        text-align: right;
                        margin-right: 0;
                    }
                }
            }
        }
    }
}
@media(max-width:480px)
{
    .review-content
    
    {
        $review-content:&;
        &__recent
        {
            flex: 0 0 260px;
            width: 260px;

        }
        &--close-sidebar
        {
            #{$review-content}__arrow
            {
                left:260px; 
             
            }
        
    
    
        }
        &__modal
        {
            .modal-dialog 
            {
                max-width: 960px;
            }
            &__content
            {
                padding: 40px ;
              
                .dropzone
                {
                    .dz-message
                    {
                        >span
                        {
                            font-size: 19px;
                        }
                    }
                }
            }
    
       
        }
      
    }
}