.main-header {
    position: relative;
    height: 600px;
    padding-top: 30px;

    &__background {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 1;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .container {
        position: relative;
        z-index: 2;
    }

    &__nav {
        display: flex;
        align-items: center;

        &__right {
            > ul {
                display: flex;
                align-items: center;

                > li {
                    margin-left: 12px;

                    &:last-child {
                        margin-left: 0;
                    }

                    > a {
                        color: #fff;
                        display: block;
                        font-size: 14px;
                        padding: 0 12px;
                        line-height: 34px;
                        font-weight: 500;
                        border-bottom: 2px solid transparent;

                        &:hover {
                            border-color: #fff;
                        }
                    }

                    > ul {
                    }

                    &:hover {
                        > ul {
                            opacity: 1;
                            visibility: visible;
                            margin-top: 10px;
                            transition-delay: 0s;
                        }
                    }
                }
            }
        }

        &__left {
            margin-right: auto;

            >ul {
                display: flex;
                align-items: center;

                > li {
                    margin-left: 12px;

                    &:last-child {
                        margin-left: 0;
                    }

                    > a {
                        border-width: 2px 0;
                        border-style: solid;
                        border-color: transparent;
                        color: #fff;
                        display: block;
                        font-size: 14px;
                        line-height: 32px;
                        font-weight: 500;
                        padding: 0 12px;
                    }

                    &:hover {
                        > a {
                            border-bottom-color: #fff;
                        }
                    }

                    &.colored {
                        > a {
                            border: 2px solid #fff;
                            border-radius: 4px;
                            transition: 0.5s;
                            padding: 0 24px;
                        }

                        &:hover {
                            > a {
                                background-color: #fff;
                                color: #666;
                            }
                        }
                    }
                }
            }
            &--loggined {
                > ul {
                    display: flex;
                    align-items: center;
                    > li {
                        width: 38px;
                        height: 38px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        text-align: center;

                        &.user {
                            width: 58px;
                            background: rgba(0, 0, 0, 0.25);
                            position: relative;

                            > a {
                                width: 100%;
                                height: 100%;
                                display: flex;
                                padding: 0;
                                border: none;
                                display: flex;
                                align-items: center;
                                text-align: center;
                                justify-content: center;
                                > img {
                                    width: 38px;
                                    height: 100%;
                                    object-fit: cover;
                                    border-radius: 0 2px 2px 0;
                                }
                                span {
                                    width: 20px;
                                }
                                &:hover {
                                    background: rgba(0, 0, 0, 0.1);
                                }
                            }
                        }
                        >a {
                            padding: 6px;
                            svg {
                                fill: #fff;
                            }
                        }
                        &:hover {
                            > a {
                                border-bottom-color: transparent;
                            }
                            background: rgba(51, 51, 51, 0.3);
                            border-radius: 3px;
                        }
                    }
                }
            }
            &__tolbar {
                background-color: #fff;
                position: absolute;
                z-index: 9999;
                width: 217px;
                top: 48px;
                left: 0;
                border: 1px solid rgba(0, 0, 0, 0.2);
                border-radius: 3px;
                box-shadow: 0 2px 8px rgba(0,0,0,0.2);
                text-align: right;
                padding: 12px;
                display: none;
                &--open
                {
                    display:block;
                }
                &__title {
                    display: flex;
                    width: 100%;
                    border-bottom: 1px solid #e6e6e6;
                    padding-bottom: 12px;
                    &__avatar {
                      
                        flex: 0 0 60px;
                        width: 60px;
                        height: 60px;
                        >a {
                            width: 100%;
                            height: 100%;
                            >img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                            }
                        }
                    }
                    &__bio {
                        padding-right: 10px;
                        flex: 0 0 calc(100% - 60px);
                        width: calc(100% - 60px);
                        &__name
                        {
                            >a
                            {
                                font-size: 14px;
                                font-weight: 500;
                                color: #0073bb;
                                padding: 0;
                             
                                &:hover
                                {
                                    text-decoration: underline;
                                }

                            }
                        }
                        &__desc
                        {
                            span
                            {
                                font-weight: 500;

                            }
                        }
                        &__detail
                        {
                            ul
                            {
                                display: flex;
                                align-items: center;
                                li
                                {
                                    display: flex;
                                    align-items: center;
                                    margin-left: 6px;
                                    svg
                                    {
                                        fill: #f15c00;
                                    }

                                }
                            }
                        }
                    }
                }
                &__action
                {
                    padding: 12px 0;
                    border-bottom: 1px solid #e6e6e6;
                    ul
                    {
                     
                        li
                        {
                            line-height: 19px;
                            margin-bottom: 6px;
                            a
                            {
                                font-weight: 500;
                                color: #0073bb;
                                font-size: 14px;
                                &:hover
                                {
                                    text-decoration: underline;
                                }

                            }
                            svg
                            {
                                fill: #666;
                                margin-left: 6px;

                            }
                      

                        }
                    }
                }
                &__log-out
                {
                    padding-top: 12px;
                    a
                    {
                        color: #0073bb;
                        font-size: 12px;
                        &:hover
                        {
                            text-decoration: underline;
                        }
                    }
                }
                &::before
                {
                    content: "";
                    border-width: 0 10px 10px 10px;
                    border-style: solid;
                    border-color: transparent transparent #fff;
                    position: absolute;
                    top: -10px;
                    left:25px;
                }
                &::after
                {
                    content: "";
                    border-width: 0 10px 10px 10px;
                    border-style: solid;
                    border-color: transparent transparent #fff;
                    position: absolute;
                    top: -9px;
                    left:25px;
                }
            }
        }
    }

    &__logo {
        margin-top: 80px;
        margin-bottom: 48px;
        text-align: center;

        img {
            width: 160px;
            height: 80px;
        }
    }

    &__search {
        background-color: #fff;
        display: flex;
        border-radius: 4px;
        margin: 0 auto 18px;

        &__right {
            $self: &;
            background-color: #fff;
            padding-right: 10px;
            position: relative;
            border-radius: 4px;

            flex: 0 0 480px;
            max-width: 480px;
            line-height: 43px;

            &--open {
                border-bottom-right-radius: 0;

                #{$self}__menu-bottom {
                    display: block;
                }
            }

            label {
                border: none;
                padding-left: 10px;
                margin-bottom: 0;
            }

            input {
                border: none;
                width: calc(100% - 55px);
            }

            &__menu-bottom {
                background-color: #fff;
                position: absolute;
                left: 0;
                right: 0;
                top: 100%;
                border-bottom-left-radius: 4px;
                border-bottom-right-radius: 4px;
                border-top: 1px solid #e6e6e6;
                display: none;
                z-index: 999;

                ul {
                    z-index: 2;
                    position: relative;

                    li {
                        line-height: 45px;
                        &:hover {
                            background-color: #0073bb;
                            a {
                                color: #fff;
                                svg {
                                    fill: #fff;
                                }
                            }
                        }
                        a {
                            color: #2b273c;
                            svg {
                                margin-right: 15px;
                                margin-left: 5px;
                                fill: #2b273c;
                            }
                        }
                    }
                }
            }
        }

        &__left {
            line-height: 43px;
            background-color: #fff;
            border-radius: 4px;
            flex: 0 0 416px;
            max-width: 416px;
            $self: &;

            position: relative;

            &--open {
                border-bottom-right-radius: 0;

                #{$self}__menu-bottom {
                    display: block;
                }
            }

            label {
                border: none;
                padding-left: 10px;
                border-right: 1px solid #e6e6e6;
                padding-right: 10px;
                margin-bottom: 0;
            }

            input {
                border: none;
                width: calc(100% - 55px);
            }

            &__menu-bottom {
                background-color: #fff;
                position: absolute;
                left: 0;
                right: 0;
                top: 100%;
                border-bottom-left-radius: 4px;
                border-bottom-right-radius: 4px;
                border-top: 1px solid #e6e6e6;
                display: none;
                z-index: 999;

                ul {
                    z-index: 2;
                    position: relative;

                    li {
                        line-height: 45px;
                        &:hover {
                            background-color: #0073bb;
                            a {
                                color: #fff;
                                svg {
                                    fill: #fff;
                                }
                            }
                        }
                        a {
                            color: #2b273c;
                            svg {
                                margin-right: 15px;
                                margin-left: 5px;
                                fill: #2b273c;
                            }
                        }
                    }
                }
            }
        }

        &__button {
            flex: 0 0 64px;

            border-bottom-left-radius: 4px;
            border-top-left-radius: 4px;
            line-height: 43px;

            button {
                padding: 0;
                border: none;
                width: 100%;
                background-color: #d32323;
                max-width: 64px;
                height: 45px;
                border-bottom-left-radius: 4px;
                border-top-left-radius: 4px;
                display: flex;
                align-items: center;
                justify-content: center;

                svg {
                    color: red;
                    font-size: 24px;
                    fill: #fff;
                }
            }
        }
    }

    &__menu-bottom {
        margin-bottom: 168px;
        display: flex;
        justify-content: center;

        > ul {
            display: flex;

            > li {
                padding: 0 12px;
                position: relative;

                > a {
                    color: #fff;
                    font-weight: 500;
                    line-height: 19px;
                    font-size: 14px;
                    display: flex;
                    align-items: center;

                    &:hover {
                        text-decoration: underline;
                    }

                    svg {
                        margin-left: 8px;
                        margin-right: 2px;
                        fill: #fff;
                    }
                }

                > ul {
                    visibility: hidden;
                    opacity: 0;
                    background-color: #fff;
                    position: absolute;
                    right: 0;
                    top: 35px;
                    transition: 0.5s;
                    z-index: 2;
                    border-radius: 3px;
                    border: 1px solid #e6e6e6;
                    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
                    width: 150px;
                    padding: 6px 0;

                    &:before {
                        content: "";
                        border-width: 0 10px 10px 10px;
                        border-style: solid;
                        border-color: transparent transparent #fff;
                        position: absolute;
                        top: -10px;
                        right: 40%;
                    }

                    > li {
                        position: relative;

                        a {
                            display: block;
                            padding: 0px 12px;
                            color: #0073bb;
                            line-height: 25px;

                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }
                }

                &:hover {
                    > ul {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
        text-align: center;

        > a {
            color: #fff;
            font-weight: 700;

            font-size: 14px;

            &:hover {
                text-decoration: underline;
            }
        }

        > span {
            color: #fff;
            font-size: 13px;

            a {
                color: #fff;
                font-weight: 700;
                font-size: 14px;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}

