.stay-gold-asked
{
    margin-bottom: 48px;
    .container {
        max-width: 1144px;
        margin: 0 auto;
    }
    &__wrapper
    {
        &__title
        {
            margin-bottom: 24px;
            h4
            {
                color: rgba(43,39,60,1);
                font-weight: 500;
                font-size: 20px;
                line-height: 26px;

            }
        }
        &__ask
        {
            margin-bottom: 32px;
            &__quastion
            {
                margin-bottom: 16px;
                p
                {
                    font-size: 14px;
                    line-height: 20px;
                    font-weight:500;
                    color: rgba(43,39,60,1);
                }

            }
            &__answer
            {
                margin-bottom: 16px;
                p
                {
                    font-size: 14px;
                    line-height: 20px;
                    color: rgba(43,39,60,1);
                }
                
            }
        }
        
    }
}
@media(max-width:480px)
{
    .stay-gold-asked
        {
            &__wrapper
            {
                &__title
                {
                    h4
                    {
                        font-size: 15px;
                    }
                }
            }
        }
}