.edit
{
    margin-bottom: 36px;
    
    &__item
    {
        max-width: 465px;
      
       
        &__title
        {
            border-bottom: 1px solid #e6e6e6;
            margin-bottom: 11px;
            display: flex;
            flex-direction: column;
            h2
            {
                color: #d32323;
                line-height: 26px;
                margin-bottom: 6px;
                font-size: 21px;
                font-weight: 500;
            }
            p
            {

            }
        }
        &__pro-edit
        {
        
           &__title
           {
            margin-bottom: 6px;
              
            h4
            {
                color: #333;
                margin-bottom: 6px;
                font-size: 14px;
                font-weight: 500;
                line-height: 16px;
                a
                {
                    color: #0073bb;
    
                }
                p
                {
                    color: #999;
                    font-size: 12px;
                    line-height: 18px;
                }

            }
         
           }
           &__image
           {
               border-radius: 4px;
               margin-bottom: 18px;
               img
               {
                  border-radius: 4px;
               }
           }
           &__form
           {

               margin-bottom: 18px;
               .form-group
               {
                 
                    label 
                    {
                        color: #333;
                        font-size: 14px;
                        font-weight: 500;
                        margin-bottom: 6px;
                        line-height: 17px;
                    }
                    p
                    {
                        color: #999;
                        font-size: 12px;
                        line-height: 18px;
                        margin-bottom: 3px;
                    }
                    input
                    {
                        margin: 0 0 18px;
                        padding: 5px 9px;
                        background: #fff;
                        border: 1px solid #999;
                        border-radius: 3px;
                        font-size: 14px;
                        line-height: 34px;
                       
                    }

                  
               }
               .form-check
               {
                   padding: 0;
                   input
                   {
                       position: static;
                       margin: 0;
                   }
                   label
                   {
                       font-size: 12px;
                   }
               }
               select.form-control
               {
                   font-size: 12px;
               }
               .form-control
               {
                   font-size: 12px;
                &:focus
                {
                 box-shadow: 0 0 4px rgba(0 ,115 ,187 ,.30);
                 outline: none;
                 color: #333;
                 border-color: #0073bb;
                }
               
                line-height: 30px;
                margin-bottom: 18px;
               }
               .btn-primary
               {

                transition: all 0s;
                background: #d32323;
                border: 1px solid #d32323;
                background-image: radial-gradient(transparent 50%, transparent 50%);
                background-position: 50% 50%;
                background-size: 50px 50px;
                font-size: 14px;
                &:hover,:visited,:active
                {
                    background-color: #dc4f4f;
                    border: 1px solid #dc4f4f;
                }
                &:focus
                {
                  
                  box-shadow:none;
                }
            }
            a
            {
                 font-size: 14px;
                 color: #0073bb;
                 margin-right: 12px;
                 &:hover
                 {
                     text-decoration: underline;
                 }
            }
               
             
             
            
             
           }
           
        }
    }
}

@media(max-width:480px)
{
    .edit
    {
        .btn-primary
        {
            width: 100%;
            margin-bottom: 7px;
        }
        a
        {
            display: flex;
            justify-content: center;
            font-size: 16px;
        }
    }
}