.brdcrumbs
{
    padding: 20px 0;
    ul
    {
        display: flex;
        align-items: center;
        li
        {
            svg
            {
                width: 15px;
                transform: rotate(180deg);
                margin: 0 5px;
            }
            a
            {
                font-weight: 600;
                color: rgba(117,114,128,1);
                &:hover
                {
                    text-decoration: underline;
                }
            }
        }
    }
}