.questions
{
    a
    {
        color: rgba(0,131,143,1);
    }
    &__title
    {
        margin-bottom: 15px;
        h1
        {
            font-size: 28px;
            font-weight: 600;
            color: rgba(43,39,60,1);
        }
    }
    &__sort
    {
        display: flex;
        align-items: center;
        border-bottom: 1px solid #eeeeef;
        padding-bottom: 20px;
        margin-bottom: 20px;
        >span
        {
            white-space: nowrap;
            margin-left: 10px;
        }
        .form-group
        {
            margin-bottom: 0;
        }
        .btn-light:not(:disabled):not(.disabled).active:focus,
        .btn-light:not(:disabled):not(.disabled):active:focus,
        .show > .btn-light.dropdown-toggle:focus
        {
            outline: unset;
            box-shadow: none;
        }
        .bootstrap-select.form-control
        {
            > .dropdown-toggle
            {
                padding: 0;
                border: none;
                background-color: transparent;
                color: rgba(0,131,143,1);
                font-size: 13px;
                display: flex;
                .filter-option
                {
                    padding-left: 10px;
                }
                &:focus
                {
                    outline: unset;
                    box-shadow: none;
                }
            }
        }
    }
    &__item
    {
        padding-bottom: 24px;
        margin-bottom: 24px;
        border-bottom: 1px solid #eeeeef;
        &__title
        {
            h2
            {
                line-height: 22px;
                font-weight: 600;
                color: rgba(43,39,60,1);
                font-size: 16px;
            }
        }
        &__author
        {
            margin-bottom: 16px;
            span
            {
                display: flex;
                align-items: center;
            }
            a
            {
                margin-right: 5px;
            }
        }
        &__row
        {
            display: flex;
        }
        &__avatar
        {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            overflow: hidden;
            position: relative;
            flex: 0 0 40px;
            max-width: 40px;
            margin-left: 15px; 
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__desc
        {
            margin-bottom: 15px;
            a
            {
                font-weight: 600;
            }
            &__info
            {
                margin-top: 10px;
                display: flex;
                flex-wrap: wrap;
                color: rgba(117,114,128,1);
                b
                {
                    margin-left: 10px;
                }
                span
                {
                    margin-left: 5px;
                    &:after
                    {
                        content: '\00a0\2022\00a0';
                        margin-right: 5px;
                    }
                    &:last-child
                    {
                        margin-left: 0;
                        &:after
                        {
                            display: none;
                        }
                    }
                }
            }
        }
        &__more
        {
            a
            {
                background-color: rgba(238,238,239,1);
                color: rgba(43,39,60,1);
                padding: 0 8px;
                border-radius: 4px;
                font-weight: 600;
                transition: 0.3s;
                &:hover
                {
                    background-color: rgb(226,226,230);
                    color: rgba(43,39,60,1);
                }
                &:focus
                {
                    background-color: rgb(226,226,230);
                    box-shadow: 0 0 4px 1px rgba(0,0,0,.35);
                }
            }
        }
    }
    &__form
    {
        margin-bottom: 50px;
        
        h3
        {
            font-size: 16px;
            font-weight: 600;
            color: rgba(43,39,60,1);
            margin-bottom: 15px;
        }
        textarea
        {
            min-height: 150px;
            max-width: 600px;
            padding: 15px;
            margin-bottom: 25px;
        }
        button
        {
            background-color: rgba(244,57,57,1);
            color: #fff;
            line-height: 40px;
            border-radius: 4px;
            border: none;
            font-weight: 600;
            padding: 0 15px;
            &:focus
            {
                background-color: rgb(249,19,27);
                box-shadow: 0 0 4px 1px rgba(0,0,0,.35);
            }
            &:hover
            {
                background-color: rgb(244,13,21);
                box-shadow: 0 1px 4px 0 rgba(0,0,0,.3);
                color: rgba(255,255,255,1);
            }
        }
    }
    &__check
    {
        margin-bottom: 10px;
        label
        {
            position: relative;
            padding-right: 25px;
            font-weight: 600;
            cursor: pointer;
            &:before
            {
                content: '';
                width: 18px;
                height: 18px;
                transition: 0.2s;
                background-color: #fff;
                border-radius: 4px;
                border: 1px solid #bbbac0;
                top: 0;
                right: 0;
                position: absolute;
            }
            &:after
            {
                content: '';
                border-width: 0 2px 2px 0;
                border-color: #fff;
                border-style: solid;
                border-radius: 0 0 2px 0;
                @include absolute(3px,6px,f,f);
                width: 6px;
                height: 11px;
                transform: rotate(45deg);
            }
        }
        input
        {
            display: none;
            &:checked
            {
                + label
                {
                    &:before
                    {
                        background-color: #00838f;
                        border-color: #00838f;
                    }
                }
            }
        }
    }
}