.intro-stay-gold
{
    margin-bottom: 24px;
    position: relative;
    
    .container
    {
        max-width: 1144px;
        margin: 0 auto;
        position: relative;
        z-index: 5;

    }
    &__image
    {
        overflow: hidden;
        position: relative;
        white-space: nowrap;
        display: flex;
        &__wrapper
        {
            height: 435px;
            white-space: nowrap;
            display: flex;
            flex: 0 0 auto;
            a
            {
                height: 100%;
            }
            img
            {
                height: 100%;
                width: auto
            }
        }
        &:before
        {
            content: '';
            @include absolute(0,0,0,0);
            background: linear-gradient(to bottom,rgba(0,0,0,.0001) 31.42%,#000);
            z-index: 2;
            pointer-events: none;
        }
        
    }
    &__text
    {
        @include absolute(f,15px,40px,f);
        z-index: 2;
        &__title
        {
            margin-bottom: 8px;

            h1
            {
                color:#fff;
                font-size: 48px;
                line-height: 54px;
                font-weight: 500;
            }
        }
        &__favorite
        {
            display: flex;
            align-items: center;
            margin: 16px 0;
            &__star
            {
                width: 176px;
                height: 32px;
                background-image: url(../img/star.png);
                background-position: 0 0;
                background-repeat: no-repeat;

                &[data-star="1"] {
                    background-position: 0 -64px;
                }

                &[data-star="1.5"] {
                    background-position: 0 -32px;
                }

                &[data-star="2"] {
                    background-position: 0 -130px;
                }

                &[data-star="2.5"] {
                    background-position: 0 -98px;
                }

                &[data-star="3"] {
                    background-position: 0 -192px;
                }

                &[data-star="3.5"] {
                    background-position: 0 -160px;
                }

                &[data-star="4"] {
                    background-position: 0 -256px;
                }

                &[data-star="4.5"] {
                    background-position: 0 -224px;
                }

                &[data-star="5"] {
                    background-position: 0 -288px;
                }
            }
            &__review
            {
                margin-right:12px;
                span
                {
                    color:#fff;
                    font-size: 16px;
                    line-height: 22px;
                    font-weight: 500;

                
                    
                }
            }
        }
        &__claimed
        {
            margin-bottom: 8px;
            display: flex;
            align-items: center;
            $claimed:&;
            &__popover
            {
                visibility: hidden;
                opacity: 0;
                position: absolute;
               bottom: 75px;
                right:25px;
                display: flex;
                 width: 300px;
                 transition: 0.5s;
                 background: #2b273c;
                 border-color: #2b273c;
                 border-radius: 4px;
                 padding: 12px;
                
                &:before
                {
                    position: absolute;
                    content: '';
                    border-width: 10px 10px 0 10px;
                    border-style: solid;
                    border-color: #2b273c transparent transparent;
                    bottom: -10px;
                    right: 15px;
                }
                &:after
                {
                    content: '';
                    border-width: 9px 9px 0 9px;
                    border-style: solid;
                    border-color: #2b273c transparent transparent;
                    position: absolute;
                    bottom: -8px;
                    right: 16px;
                }
                >p
                 {
                    color: #fff;
                    font-size: 14px;
                    line-height: 20px;
                    >a
                    {
                        font-size: 14px;
                        line-height: 20px;
                        display: flex;
                        color: #0ab3c9;
                        &:hover
                        {
                            text-decoration: underline;
                        }

                    }

                 }
            }
            svg
            {
                fill: rgba(10,179,201,1);
                font-size:16px ;
                margin-left: 4px;
                font-weight: 400;
            }
            span
            {
                color: rgba(10,179,201,1);
                font-size: 16px;
                line-height: 22px;
                font-weight: 400;
                &:hover
                {
                    #{$claimed}__popover
                    {
                            visibility:visible;
                           opacity: 1;
                    }
                }
                &.daler
                {
                   color:#fff;
                   padding: 0 6px;
                }
              
   
            }
           
            >a

            {
                color: #fff;
                font-size: 16px;
                line-height: 22px;
                font-weight: 400;
                margin-right: 4px;
                display: flex;

            }
            &__edit
            {
                margin-right: 6px;
                a
                {
                display: flex;

                    background-color: rgba(255,255,255,0.1);
                        background-image: radial-gradient(rgba(0,0,0,0.05) 1%,transparent 1%);
                        font-size: 12px;
                        line-height: 18px;
                        color: #fff;
                        border: 1px solid transparent;
                        border-radius: 4px;
                        padding: 0 6px;
                        transition: 0.5s;
                        &:hover
                        {
                        background-color: rgba(255,255,255,0.3);
                        color: rgba(255,255,255,1);
                        box-shadow: none;
                        }

                }
            }
        }
        &__open
        {
            margin-bottom: 8px;
            align-items: center;
            display: flex;
            span
            {
                font-size: 16px;
                line-height: 22px;
                font-weight: 400;
                color: rgba(5,168,130,1);
            }
            p
            {
                font-size: 16px;
                line-height: 22px;
                font-weight: 400;
                margin-right: 8px;
                &.hours
                {
                    color: #fff;

                }
                &.update
                {
                    svg
                    {
                        fill:rgba(10,179,201,1);
                        margin-right: 8px;
                    }
                    color: rgba(10,179,201,1);
                }

                
            }
           
        }
    }
    &__button
    {
        @include absolute(f,f,40px,15px);
      
        
        a
        {
            
            padding: 0 32px;
            line-height: 48px;
            display: flex;
            border: 1px solid #fff;
            border-radius: 4px;
            transition: 0.5s;
            font-size: 16px;
            color: #fff;
            &:hover
            {
                background-color: rgba(255,255,255,0.3);
                background-image: radial-gradient(rgba(255,255,255,0.1) 1%,transparent 1%);
            }

        }
    }
}

@media(max-width:992px)
{
    .intro-stay-gold
    {
        &__image
        {
            &__wrapper
            {
                height: 350px;
            }
        }
        &__text
        {
            &__title
            {
                h1
                {
                    font-size: 35px;
                }
            }
            &__favorite
            {
                margin:8px 0;
            }
            &__claimed
            {
                span
                {
                    font-size: 14px;
                }
                a
                {
                    font-size: 14px;
                }
            }
            &__open
            {
                span
                {
                    font-size: 14px;
                }
                p
                {
                    font-size: 14px;
                }
            }
         
        }
        &__button
        {
            a
            {
                font-size: 14px;
                padding: 0 20px;
            }
        }
    }
}
@media(max-width:768px)
{
    .intro-stay-gold
    {
      
        &__text
        {
            display: flex;
            flex-direction: column;
            align-items: center;
            left: 15px;
            bottom: 100px;

        }
        &__button
        {
            left: 15px;
            right: 15px;
            display: flex;
            justify-content: center;
        }
    }
}
@media(max-width:576px)
{
    .intro-stay-gold
    {
        &__image
        {
            &__wrapper
            {
                height: 300px;
            }
        }
        &__text
        {
            &__open
            {
                p
                {
                    font-size: 12px;
                }
                span
                {
                    font-size: 12px;
                }
                svg
                {
                    margin-right: 4px;
                }
            }
        }
    }
}
@media(max-width:480px)
{
    .intro-stay-gold
    {
        &__image
        {
            display:none;
        }
        &__text
        {
            border-bottom: 1px solid #eeeeef;
            position:static;
            align-items: flex-start;
            &__title
            {
                margin-bottom:0px;
                h1
                {
                    color: #333;
                   padding-top: 6px;
                   font-size: 22px;
                }
            }
            &__favorite
            {
                &__review
                {
                    span
                    {
                        font-size: 12px;
                        color: #999;
                    }
                }
            }
            &__claimed
            {
                margin-bottom: 0;
                svg
                {
                    display: none;
                }
                span
                {
                    display: none;
                    &.daler
                    {
                        display: block;
                        color: #999;
                        font-size: 12px;
                    }
                }
                a
                {
                    color: #999;
                    font-size: 12px;
                }
                &__edit
                {
                    display: none;
                }
            }
            &__open
            {
                flex-wrap: wrap;
                span
                {
                    color: #999;
                }
                p
                {
                    color: #999;
                    &.hours
                    {
                    color: #999;

                    }
                    &.update
                    {
                        margin-right: 0;
                        flex: 0 0 100%;
                        width: 100%;
                        svg
                        {
                            margin-right: 0;
                        }

                    }
                }
            }
         
        }
        &__button
        {
            display: none;
        }
    }
}