.upload-pic
{
    padding: 30px 0 80px;

    .dropzone
    {
        border: 3px dashed #999;
        border-radius: 8px;
        padding: 0;
        margin: 0 15px;
        form
        {
            padding: 50px 15px;
            cursor: pointer;
        }
        &.loaded
        {
            border: none;
            margin: 0;
            form
            {
                padding: 0;
            }
        }
        .dz-message
        {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            >span
            {
                font-weight: 600;
                font-size: 28px;
                color: #333;
                margin-bottom: 15px;
            }
            .hr-line
            {
                position: relative;
                margin-bottom: 15px;
                font-size: 12px;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 700px;
                max-width: 100%;
                span
                {
                    position: relative;
                    z-index: 2;
                    padding: 0 15px;
                    background-color: #fff;
                    font-size: 14px;
                }
                &:before
                {
                    content: '';
                    position: absolute;
                    width: 500px;
                    max-width: 90%;
                    top: 50%;
                    height: 1px;
                    background-color: #ccc;
                    z-index: 1;
                }
            }
            button
            {
                color: #fff;
                background: #d32323;
                padding: 0 35px;
                line-height: 36px;
                border: none;
                border-radius: 3px;
                font-size: 13px;
                font-weight: 600;
                display: inline-block;
            }
        }
        .dz-image
        {
            display: flex;
            align-items: center;
            justify-content: center;
            width: unset;
        }
        .dz-preview
        {
            label
            {
                display: block;
                margin-bottom: 10px;
            }
            textarea
            {
                width: 100%;
                height: 90px;
                border: 1px solid #ddd;
                border-radius: 4px;
                padding: 10px;
                background-color: transparent;
            }
        }
    }
    &__header
    {
        margin-bottom: 12px;
        h3
        {
            font-size: 16px;
            display: none;
        }
    }
    &__footer
    {
        display: flex;
        justify-content: flex-end;
        margin-top: 15px;
        display: none;
        button
        {
            background-color: #f43939;
            color: white;
            transition: all 0.8s;
            line-height: 40px;
            padding: 0 16px;
            border-radius: 4px;
            text-align: center;
            align-items: center;
            font-weight: 500;
            font-size: 16px;
            border: none;
            
        }
        a
        {
            display: inline-block;
            background-color: #fff;
            color: #222;
            transition: all 0.8s;
            line-height: 38px;
            padding: 0 16px;
            border-radius: 4px;
            text-align: center;
            align-items: center;
            font-weight: 500;
            font-size: 16px;
            border: 1px solid #ddd;
            margin-left: 15px;
        }
    }
}