.collections-including
{
    .swiper

	{
		&-btn
		{
			
			&-next
			{
				box-shadow: 0 0 10px rgba(0,0,0,.1);
				background: #fff;
				border: 1px solid #eeeeef;
				border-radius: 50%;
				height: 24px;
				width: 24px;
				top: 35%;
				right:10px;
				position: absolute;
				z-index: 2;
				text-align: center;
				display: flex;
				align-items: center;
				opacity: .8;
				svg
				{
	

				}
			}
		}
	}
        .container {
            max-width: 1144px;
            margin: 0 auto;
        }
       &__item
       {
        border-bottom: 1px solid #eeeeef;
		padding-bottom:48px;
        margin-bottom:48px;
            &__title
            {
                margin-bottom: 24px;
                h4
                {
                    color: rgba(43,39,60,1);
                    font-weight: 500;
                    font-size: 20px;
                }
            }
            &__slider
            {
                
            
                &__image
                {
                    margin-bottom: 16px;
                    border-radius: 4px;
                    height: 218px;
                    position: relative;
                
                    a
                    {
                        img
                        {
                        border-radius: 4px;
                        width: 100%;
                        height: 100%;
                        object-fit:cover;

        
                        }
                    }
                    &__save
                    {
                        @include absolute(f,f,0,0);
                        width:109px ;
                        height:109px;
                        background-color: rgba(0,0,0,.3);
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        text-align: center;
                        backdrop-filter: blur(5px);
                        pointer-events: none;
                        
                        span
                        {
                            font-size: 28px;
                            display: flex;
                            align-items: center;
                            color: #fff;
                            font-weight: 500;
                            svg
                            {
                                fill: #fff;
                            }
                        }

                    }
        
                }
                &__text
                {
                    p
                    {
                        font-size: 16px;
                        line-height: 22px;
                        font-weight:500;
                        color: rgba(43,39,60,1);
                    }
                    span
                    {
                        font-size: 14px;
                        line-height: 20px;
                        color: rgba(117,114,128,1);
        
                    }
        
                }
            }
       }
}

@media(max-width:480px)
{
    .collections-including
    {
        &__item
        {
            &__title
            {
                h4
                {
                    font-size: 14px;
                }
            }
        }
    }
}