.stay-gold-people
{
    .swiper

	{
		&-btn
		{
			
			&-next
			{
				box-shadow: 0 0 10px rgba(0,0,0,.1);
				background: #fff;
				border: 1px solid #eeeeef;
				border-radius: 50%;
				height: 24px;
				width: 24px;
				top: 35%;
				right:10px;
				position: absolute;
				z-index: 2;
				text-align: center;
				display: flex;
				align-items: center;
				opacity: .8;
				svg
				{
	

				}
			}
		}
	}
    .container {
		max-width: 1144px;
		margin: 0 auto;
	}


   &__content
   {
    border-bottom: 1px solid #eeeeef;
    padding-bottom: 48px;
    margin-bottom: 48px;
    &__title
    {
        margin-bottom: 24px;
        h4
        {
            color: rgba(43,39,60,1);
            font-weight:500;
            font-size: 20px;
            line-height: 26px;

        }
    }
    &__slider
    {
      &__item
      {
        &__image
        {
            height: 186px;
            border-radius: 4px;
            margin-bottom: 12px;

            a
            {
                img
                {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                    border-radius: 4px;

                }
            }
        }
        &__title
        {
            margin-bottom: 4px;
            a
            {
                font-size: 16px;
                line-height: 22px;
                font-weight:500;
                color: rgb(43, 39, 60);

            }
        }
        &__score {

			display: flex;
			align-items: center;
			margin-bottom:4pxpx;

			&__star {
				margin-left: 8px;
				width: 108px;
				height: 20px;
				background-image: url(../img/star.png);
				background-repeat: no-repeat;

				&[data-star="1"] {
					background-position: 0 -320px;
				}

				&[data-star="1"] {
					background-position: 0 -360px;
				}

				&[data-star="1.5"] {
					background-position: 0 -340px;
				}

				&[data-star="2"] {
					background-position: 0 -400px;
				}

				&[data-star="2.5"] {
					background-position: 0 -380px;
				}

				&[data-star="3"] {
					background-position: 0 -440px;
				}

				&[data-star="3.5"] {
					background-position: 0 -420px;
				}

				&[data-star="4"] {
					background-position: 0 -480px;
				}

				&[data-star="4.5"] {
					background-position: 0 -460px;
				}

				&[data-star="5"] {
					background-position: 0 -500px;
				}


			}

			&__count
			{
				span
				{
                    font-size: 14px;
                    line-height: 20px;
                    color: rgba(117,114,128,1);
				}
			}
        }
        &__sub-score
        {
            span
            {
                font-size: 12px;
                line-height: 18px;
                color: rgba(117,114,128,1);
            }
        }
      }
    }
   }
}