                }
                &__popover
                {
                    visibility: hidden;
                    opacity: 0;
                    background-color: #fff;
                    position: absolute;
                   bottom: 30px;
                    right: 0;
                    display: flex;
                    border: 1px solid #ccc;
                    padding: 12px;
                    border-radius: 5px;
                    box-shadow: 0 2px 6px rgba(0 ,0, 0 ,.10);
                     width: 300px;
                    &:before
                    {
                        position: absolute;
                        content: '';
                        border-width: 10px 10px 0 10px;
                        border-style: solid;
                        border-color: #ccc transparent transparent;
                        bottom: -10px;
                        right: 15px;
                    }
                    &:after
                    {
                        content: '';
                        border-width: 9px 9px 0 9px;
                        border-style: solid;
                        border-color: #fff transparent transparent;
                        position: absolute;
                        bottom: -8px;
                        right: 16px;
                    }
                    &__image
                    {
                     
                        width: 90px;
                        height: 90px;
                        flex: 0 0 90px;
                        max-width: 90px;
                        margin-left: 8px;
                        border-radius: 4px;
                        >a
                       {
                           
                        img
                        {
                        border-radius: 4px;
                        height: 100%;
                        width: 100%;
                        object-fit: cover;


                        }
                       }
                        

                    }
                    &__text
                    {
                        flex: 0 0 calc(100% - 90px);
                        max-width:calc(100% - 90px) ;
                      
                        &__title
                        {
                            margin-bottom: 6px;
                            a
                            {
                                color: #0073bb;
                                &:hover
                                {
                                    text-decoration: underline;

                                }

                            }
                        }
                        &__star
                        {
                            display: flex;
                            
                            &__icon
                            {
                                margin-left: 8px;
                                width: 108px;
                                height: 20px;
                                background-image: url(../img/star.png);
                                background-repeat: no-repeat;
            
                                &[data-star="1"] {
                                    background-position: 0 -320px;
                                }
            
                                &[data-star="1"] {
                                    background-position: 0 -360px;
                                }
            
                                &[data-star="1.5"] {
                                    background-position: 0 -340px;
                                }
            
                                &[data-star="2"] {
                                    background-position: 0 -400px;
                                }
            
                                &[data-star="2.5"] {
                                    background-position: 0 -380px;
                                }
            
                                &[data-star="3"] {
                                    background-position: 0 -440px;
                                }
            
                                &[data-star="3.5"] {
                                    background-position: 0 -420px;
                                }
            
                                &[data-star="4"] {
                                    background-position: 0 -480px;
                                }
            
                                &[data-star="4.5"] {
                                    background-position: 0 -460px;
                                }
            
                                &[data-star="5"] {
                                    background-position: 0 -500px;
                                }
                            }
                            &__text
                            {
                                p
                                {
                                    color: #666;
                                    font-size: 12px;

                                }
                            }
                        }
                        &__desc
                        {
                            display: flex;
                            flex-direction: column;
                            height: 36px;
                            overflow: hidden;
                            span
                            {
                                color: #333;
                                font-size: 12px;
                            }
                        }

                    }
                }
                &:hover
                {
                   #{$title}__popover
                    {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }

            &__like {
                padding: 8px 0 0;
