.questions-detail
{
    padding-bottom: 35px;
    &__content
    {
        border-left: 1px solid #e6e6e6;
        padding-left: 15px;
        &__title
        {
            display: flex;
            align-items: center;
            margin-bottom: 10px;
            h2
            {
                font-size: 21px;
                font-weight: 600;
                color: #333;
            }
            a
            {
                margin-right: auto;
                width: 30px;
                height: 30px;
                line-height: 28px;
                border: 1px solid #ccc;
                border-radius: 3px;
                font-size: 12px;
                color: #999;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: 0.2s;
                svg
                {
                    fill: #999;
                    transition: 0.2s;
                }
                &:hover
                {
                    color: #333;
                    background: #f8f8f8;
                    background: linear-gradient(#fff,#eee);
                    border-color: #cdcdcd;
                    border-color: rgba(0,0,0,.2);
                    box-shadow: 0 1px 1px rgba(0,0,0,.1), inset 0 1px 0 hsla(0deg,0%,100%,.5);
                    svg
                    {
                        fill: #111;
                    }
                }
            }
        }
        &__info
        {
            color: #999;
            margin-bottom: 15px;
        }
        &__check
        {
            margin-bottom: 20px;
            input
            {
                position: relative;
                top: 3px;
                margin-left: 5px;
            }
            label
            {
                margin-bottom: 0;
            }
        }
        &__answers-header
        {
            border-bottom: 1px solid #e6e6e6;
            display: flex;
            align-items: center;
            padding-bottom: 6px;
            h3
            {
                font-size: 16px;
                color: #d32323;
            }
            &__sort
            {
                margin-right: auto;
                display: flex;
                align-items: center;
                >span
                {
                    white-space: nowrap;
                    margin-left: 5px;
                    color: #0073bb;
                }
                .form-group
                {
                    margin-bottom: 0;
                }
                .btn-light:not(:disabled):not(.disabled).active:focus,
                .btn-light:not(:disabled):not(.disabled):active:focus,
                .show > .btn-light.dropdown-toggle:focus
                {
                    outline: unset;
                    box-shadow: none;
                }
                .bootstrap-select.form-control
                {
                    > .dropdown-toggle
                    {
                        padding: 0;
                        display: flex;
                        border: none;
                        background-color: transparent;
                        color: #0073bb;
                        font-size: 12px;
                        font-weight: 600;
                        .filter-option
                        {
                            padding-left: 10px;
                        }
                        &:focus
                        {
                            outline: unset;
                            box-shadow: none;
                        }
                    }
                }
            }
        }
        &__answers-item
        {
            padding: 18px 0;
            border-bottom: 1px solid #e6e6e6;
            &__header
            {
                display: flex;
                align-items: center;
                margin-bottom: 12px;
                &__avatar
                {
                    width: 30px;
                    height: 30px;
                    margin-left: 10px;
                    img
                    {
                        width: 30px;
                        height: 30px;
                        object-fit: cover;
                        border-radius: 4px;
                    }
                }
                &__name
                {
                    display: flex;
                    flex-direction: column;
                    line-height: 15px;
                    a
                    {
                        font-weight: 600;
                    }
                    span
                    {
                        font-size: 13px;
                    }
                    small
                    {
                        color: #999;
                        font-size: 12px;
                    }
                    ul
                    {
                        display: flex;
                        li
                        {
                            display: flex;
                            margin-left: 5px;
                            align-items: center;
                            color: #666;
                            svg
                            {
                                fill: #f15c00;
                                margin-left: 3px;
                            }
                        }
                    }
                }
                &__date
                {
                    margin-right: auto;
                    color: #999;
                }
            }
            &__desc
            {
                margin-bottom: 12px;
                margin-right: 40px;
            }
            &__btns
            {
                display: flex;
                align-items: center;
                a
                {
                    &:not(.left-view)
                    {
                        line-height: 28px;
                        color: #666;
                        background: #fff;
                        border: 1px solid #ccc;
                        padding: 0 8px;
                        margin-left: 3px;
                        transition: 0.2s;
                        display: flex;
                        align-items: center;
                        font-weight: 600;
                        border-radius: 3px;
                        svg
                        {
                            fill: #666;
                            transition: 0.2s;
                            margin-left: 3px;
                        }
                        span
                        {
                            margin-right: 4px;
                            font-weight: 400;
                        }
                        &:hover
                        {
                            background-color: #f5f5f5;
                        }
                        &:focus
                        {
                            background-image: radial-gradient(#e6e6e6 50%,transparent 0);
                            background-color: #e6e6e6;
                        }
                    }
                    &.left-view
                    {
                        margin-right: auto;
                        width: 30px;
                        height: 30px;
                        line-height: 28px;
                        border: 1px solid #ccc;
                        border-radius: 3px;
                        font-size: 12px;
                        color: #999;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        transition: 0.2s;
                        svg
                        {
                            fill: #999;
                            transition: 0.2s;
                        }
                        &:hover
                        {
                            color: #333;
                            background: #f8f8f8;
                            background: linear-gradient(#fff,#eee);
                            border-color: #cdcdcd;
                            border-color: rgba(0,0,0,.2);
                            box-shadow: 0 1px 1px rgba(0,0,0,.1), inset 0 1px 0 hsla(0deg,0%,100%,.5);
                            svg
                            {
                                fill: #111;
                            }
                        }
                    }
                }
            }
        }
        &__form
        {
            padding-top: 20px;
            h3
            {
                font-size: 14px;
                font-weight: 600;
                margin-bottom: 5px;
            }
            textarea
            {
                min-height: 80px;
                padding: 10px;
                margin-bottom: 15px;
                border-color: #999;
            }
            button
            {
                background-color: rgba(244,57,57,1);
                color: #fff;
                line-height: 40px;
                border-radius: 4px;
                border: none;
                font-weight: 600;
                padding: 0 15px;
                &:focus
                {
                    background-color: rgb(249,19,27);
                    box-shadow: 0 0 4px 1px rgba(0,0,0,.35);
                }
                &:hover
                {
                    background-color: rgb(244,13,21);
                    box-shadow: 0 1px 4px 0 rgba(0,0,0,.3);
                    color: rgba(255,255,255,1);
                }
            }
        }
    }
    &__sidebar
    {
        padding-right: 15px;
        &__location
        {
            display: flex;
            margin-bottom: 18px;
            &__image
            {
                width: 60px;
                height: 60px;
                margin-left: 12px;
                flex: 0 0 60px;
                max-width: 60px;
                img
                {
                    width: 100%;
                    height: 100%;
                    border-radius: 4px;
                }
            }
            &__content
            {
                &__title
                {
                    margin-bottom: 3px;
                    a
                    {
                        font-weight: 600;
                        &:hover
                        {
                            text-decoration: underline;
                        }
                    }
                }
                &__star
                {
                    display: flex;
                    align-items: center;
                    margin-bottom: 3px;
                    &__icon-star
                    {
                        margin-left: 8px;
                        width: 88px;
                        height: 16px;
                        background-image: url(../img/star.png);
                        background-repeat: no-repeat;

                        &[data-star="0"] {
                            background-position: 0 -520px;
                        }

                        &[data-star="1"] {
                            background-position: 0 -552px;
                        }

                        &[data-star="1.5"] {
                            background-position: 0 -536px;
                        }

                        &[data-star="2"] {
                            background-position: 0 -584px;
                        }

                        &[data-star="2.5"] {
                            background-position: 0 -568px;
                        }

                        &[data-star="3"] {
                            background-position: 0 -616px;
                        }

                        &[data-star="3.5"] {
                            background-position: 0 -600px;
                        }

                        &[data-star="4"] {
                            background-position: 0 -648px;
                        }

                        &[data-star="4.5"] {
                            background-position: 0 -632px;
                        }

                        &[data-star="5"] {
                            background-position: 0 -664px;
                        }
                    }
                }
            }
        }
        &__other-questions
        {
            &__title
            {
                border-bottom: 1px solid #e6e6e6;
                display: flex;
                align-items: center;
                padding-bottom: 6px;
                margin-bottom: 12px;
                h3
                {
                    font-size: 16px;
                    color: #d32323;
                }
            }
            &__item
            {
                margin-bottom: 12px;
                span
                {
                    display: flex;
                    margin-bottom: 12px;
                }
            }
        }
    }
}


@media(max-width: 992px)
{
    .questions-detail
    {
        &__content
        {
            border-left: none;
            padding-left: 0;
        }
        &__sidebar
        {
            display: none;
        }
    }
}