.first-review
{
    background-color: #f5f5f5;
    padding: 36px 0;
&__item
{
  
    &__title
    {
        margin-bottom: 18px;
        h1
        {
            color: #333;
           padding: 6px 0;
            font-size: 30px;
            font-weight: 500;
        }
    }
    &__text
    {
        display: flex;
        align-items: center;
        margin-bottom: 24px;
        
        p
        {
            margin-bottom: 0;
            font-size: 16px;
            line-height: 24px;
            color: #333;

            a
            {
                color: #0073bb;
                font-size: 16px;
                &:hover{
                    text-decoration: underline;
                }

            }
        }
    }
    &__search
    {
        display: flex;
        align-items: center;
        width: 100%;
        position: relative;
        &__find
        {
            background-color: #fff;
            border: 1px solid #e6e6e6;
            flex: 0 0 calc(50% - 24px);
            max-width: calc(50% - 24px);        
            border-radius: 3px;
            line-height:24px;
            display: flex;
            margin-left:6px;
      

            label
            {
                padding-right:5px;
                font-size: 14px;
                font-weight: 500;
                flex: 0 0 65px;
                max-width: 65px;
                margin-bottom: 0;


            }
            input
            {
                border: none;
                font-size: 14px;
                flex: 0 0 calc(100% - 65px);
                max-width:calc(100% - 65px) ;
        


            }
      
        }
        &__near
        {
            background-color: #fff;
            border: 1px solid #e6e6e6;
            flex: 0 0 calc(50% - 24px);
            max-width: calc(50% - 24px);        
            border-radius: 3px;
            line-height:24px;
            display: flex;
            margin-left: 6px;
            position: relative;
            $self: &;

            &--open {
                border-bottom-right-radius: 0;

                #{$self}__menu-bottom {
                    display: block;
                }

            }


            label
            {
                padding-right: 5px;
                font-size: 14px;
                font-weight: 500;
                flex: 0 0 60px;
                max-width: 60px;
                margin-bottom: 0;

            }
            input
            {
                border: none;
                flex: 0 0 calc(100% - 60px);
                max-width:calc(100% - 60px) ;
                font-size: 14px;
                
            }

            &__menu-bottom {
                background-color: #fff;
                box-shadow: 0 21px 38px rgba(0,0, 0, 0.2);
                border-radius: 0 0 4px 4px;
                position: absolute;
                left: 0;
                right: 0;
                top: 100%;
                border-top: 1px solid #e6e6e6;
                display: none;
                z-index: 2;

                ul {
                    z-index: 2;
                    position: relative;

                    li {
                       
                       
                        line-height:33px;
                    
                    
                        a {
                            color: #2b273c;
                            margin:0px 15px;
                            svg
                            {
                                margin:0 6px ;
                            }
                            &:first-child
                            {
                                margin-top:8px;
                            }
                            &:last-child
                            {
                                margin-bottom:8px;
                            }
                        }
                        &:hover
                        {
                            background-color: #0073bb;
                            a

                            {
                                color: #fff;
                                svg
                                {
                                    fill: #fff;
                                }
                            }

                        }
                    }
                }
            }
        }
        button
        {
            color: #fff;
            background: #d32323;
            border: 1px solid #d32323;
            flex: 0 0 36px;
            max-width: 36px;
            line-height: 24px;
            border-radius: 3px;
            text-align: center;
            &:hover
            {
                background-color: #dc4f4f;
            border: 1px solid #dc4f4f;


            }
            svg
            {
                fill: #fff;
            }
        }
    }
}
    &__image
    {
        img
        {
            width: 260px;
            height: 160px;
        }

    }
  
}

@media(max-width:767px)
{
    .first-review
    {
        &__image
        {
            text-align:center;
        }
    }
}

@media(max-width:480px)
{
    .first-review
    {
        &__item
        {
            &__title
            {
                h1
                {
                    font-size: 16px;
                }
            }
        }
    }
}