.all-photos-content
{
    padding-top: 15px;
    padding-bottom: 36px;
    &__wrapper
    {
        margin-top: 18px;
        &__title
        {
            margin-bottom:12px;
            h1
            {
                font-weight: 500;
                font-size: 30px;
                color: #333;
            }
        }
        &__profile
        {
            margin-bottom: 18px;
            display: flex;
            &__avatar
            {
                display: flex;
                &__image
                {
                    margin-left: 6px;
                    margin-top: 3px;
                    width: 30px;
                    height: 30px;
                    border-radius: 4px;
                    a
                    {
                        width: 100%;
                        height: 100%;
                        img
                        {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            border-radius: 4px;

                        }
                    }

                }
                &__bio
                {
                    &__name
                    {
                        a
                        {
                            color: #0073bb;
                            font-weight: 500;
                            &:hover
                            {
                                text-decoration: underline;
                            }
                        }

                    }
                    &__score
                    {
                        display: flex;
                        align-items: center;
                        &__star {
                            margin-left: 8px;
                            width: 108px;
                            height: 20px;
                            background-image: url(../img/star.png);
                            background-repeat: no-repeat;
            
                            &[data-star="1"] {
                                background-position: 0 -320px;
                            }
            
                            &[data-star="1"] {
                                background-position: 0 -360px;
                            }
            
                            &[data-star="1.5"] {
                                background-position: 0 -340px;
                            }
            
                            &[data-star="2"] {
                                background-position: 0 -400px;
                            }
            
                            &[data-star="2.5"] {
                                background-position: 0 -380px;
                            }
            
                            &[data-star="3"] {
                                background-position: 0 -440px;
                            }
            
                            &[data-star="3.5"] {
                                background-position: 0 -420px;
                            }
            
                            &[data-star="4"] {
                                background-position: 0 -480px;
                            }
            
                            &[data-star="4.5"] {
                                background-position: 0 -460px;
                            }
            
                            &[data-star="5"] {
                                background-position: 0 -500px;
                            }
            
            
                        }
                        &__view
                        {
                            span
                            {
                                color: #666;
                                font-size: 12px;

                            }

                        }
                    }
                }

            }
            &__add
            {
                display: flex;
                margin-right: auto;
                a
                {
                   
                    color: #fff;
                    background: #d32323;
                    border: 1px solid #d32323;
                    display: flex;
                    line-height: 34px;
                    transition: all 0.6s;
                    padding: 0 20px;
                    border-radius: 3px;
                    font-size: 14px;
                    align-items: center;
                    svg
                    {
                        margin-left:4px;
                        fill: #fff;
                    }
                    &:hover
                    {
                        background-color: #dc4f4f;

                    }
                }
            }
        }
        &__tabs
        {
            margin-bottom:18px;
            border-bottom: 1px solid #e6e6e6;
            display: flex;
            align-items: center;
            &__list
            {
            display: flex;

                align-items: center;
                flex:0 0 calc(100% - 202px);
                width:  calc(100% - 202px);
                svg
                {
                    fill: #e6e6e6;
                    margin-left: 5px;
                    cursor: pointer;
                    &:last-child
                    {
                        margin-right: auto;
                    }

                }
                ul
                {
                    display: flex;
                    align-items: center;
                    li
                    {
                        cursor: pointer;
                        color: #666;
                        padding:6px 12px;
                        line-height: 30px;
                        border:3px solid transparent;
                        font-size: 15px;

                        span
                        {
                            padding-right: 3px;
                        }
                        &.active
                        {
                            color: #333;
                            border-bottom-color: #d32323;
                        }
                        &:hover
                        {
                            background-color: #f5f5f9;
                            border-bottom-color: #d32323;
                        }

                    }
                }
            }
            &__search
            {
                display: flex;
            align-items: center;
            flex: 0 0 202px;
            width: 202px;
      
                &__input
                {
                    flex: 0 0 calc(100% - 37px);
                    width:calc(100% - 37px) ;
                  
                    input
                    {
                        background: #fff;
                        border: 1px solid #999;
                        border-radius: 0 3px 3px 0;
                        font-size: 14px;
                        line-height: 24px;
                        width: 100%;
                        border-left: none;
                    
                      

                    }
                }
                &__button
                {
                    flex: 0 0 37px;
                    width: 37px;
                     height: 28px;
                    button
                    {
                        color: #fff;
                        background: #d32323;
                        border: 1px solid #d32323;
                        transition: all 0.6s;
                        display: flex;
                        height: 28px;
                        align-items: center;
                        border-radius: 3px 0 0 3px;
                        width: 100%;
                        text-align: center;
                        justify-content: center;
                        svg
                        {
                            fill:#fff;

                        }
                        &:hover,:focus
                        {
                            background-color: #dc4f4f;
                            border:none;
                            outline: none;
    
                        }
                    }
                }
            }
        }
        &__gallery
        {
            margin-bottom: 6px;
            display: none;
            ul
            {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                margin: 0 -12px;
                li
                {
                    width: 150px;
                    height: 150px;
                    margin: 0 12px 12px 0;

                    a
                    {
                        width: 100%;
                        height: 100%;
                        img
                        {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            border-radius: 4px;

                        }
                    }
                }
            }
            &.active
            {
                display:flex;
            }
        }
        &__pagination
        {
            border-bottom: 1px solid #e6e6e6;
            border-top: 1px solid #e6e6e6;
            padding:12px 0;
            span
            {
                color: #333;
                font-size: 14px;
            }
        }
    }

    &__modal
    {
        &__content
        {
            padding: 12px 0 12px 12px;
            background-color: #fff;
            display: flex;
            flex-direction: column;
            height: 100%;
            &__title
            {
                margin-bottom: 6px;
                display: flex;
            
                &__avatar
                {
                    margin-left: 10px;
                    border-radius: 4px;
                    flex:0 0 39px;
                    max-width: 39px;
                    a
                    {
                        display: flex;
                        width: 39px;
                        height: 36px;
                        img
                        {
                            border-radius: 4px;
                            width: 100%;
                            height: 100%;

                        }
                    }
                }
                &__info
                {
                    flex:0 0 calc(100% - 49px) ;
                max-width: calc(100% - 49px);
                    &__name
                    {
                    
                        a
                        {
                            text-overflow: ellipsis;
                            overflow: hidden;
                            white-space: nowrap;
                            display: block;
                            color: #0073bb;
                            font-size: 14px;
                            font-weight: 500;
                            &:hover
                            {
                                text-decoration: underline;
                            }

                        }
                        
                    }
                    &__status
                    {
                        ul
                        {
                            display: flex;
                            align-items: center;
                            li
                            {
                                margin-left: 6px;
                                &:last-child
                                {
                                    margin-left: 0;
                                }
                                svg
                                {
                                    fill: rgb(241, 92, 0);
                                width: 18px;
                                height: 18px;
                                }
                                span
                                {
                                    color: #666;
                                    font-weight: 500;
                                }
                                a
                                {
                                    color: #f15c00;
                                    font-weight: 500;
                                    &:hover
                                    {
                                        text-decoration: underline;
                                    }

                                }

                            }
                        }
                    }
                }
            }
            &__date
            {
                margin-bottom: 12px;
                span
                {
                    color: #999;
                    font-size: 12px;

                }
            }
            &__desc
            {
                p
                {
                    color: #333;
                    margin-bottom: 6px;

                }
            }
            &__button
            {
                margin-bottom: 12px;
             
                ul
                {
                    display: flex;
                    align-items: center;
                    li
                    {
                        margin-right: 5px;
                        &:first-child
                        {
                            margin-right: 0;
                        }
                        a
                        {
    
                            display: flex;
                            color: #666;
                            background: #fff;
                            border: 1px solid #ccc;
                            line-height: 28px;
                            transition: all 0.6s;
                            font-size: 12px;
                            padding: 0 8px;
                            align-items: center;
                            border-radius: 4px;
                            svg
                            {
                                fill: #666;
                            }
                            &:hover
                            {
                                background-color: #f5f5f5;
                            }
    
                        }

                    }
                }
            }
            &__footer
            {
                border-top: 1px solid #ebc074;
                 padding-top: 8px;
                margin-top: auto;
                &__title
                {
                    margin-bottom: 6px;
                    display: flex;
                    align-items: center;
                    span
                    {
                        background: #ebac2c;
                        color: #fff;
                        font-size: 12px;
                        padding: 0 4px;
                        border-radius: 3px;
                        font-weight: 500;
                        margin-left: 2px;

                    }
                    a
                    {
                        color: #0073bb;
                        &:hover
                        {
                            text-decoration: underline;
                        }

                    }
                    svg

                    {
                        margin-right: auto;
                        fill: #ebac2c;

                    }
                }
                &__score
                {
                    margin-bottom:6px;
                    display: flex;
                    align-items: center;
                    &__star {
                        margin-left: 8px;
                        width: 108px;
                        height: 20px;
                        background-image: url(../img/star.png);
                        background-repeat: no-repeat;
        
                        &[data-star="1"] {
                            background-position: 0 -320px;
                        }
        
                        &[data-star="1"] {
                            background-position: 0 -360px;
                        }
        
                        &[data-star="1.5"] {
                            background-position: 0 -340px;
                        }
        
                        &[data-star="2"] {
                            background-position: 0 -400px;
                        }
        
                        &[data-star="2.5"] {
                            background-position: 0 -380px;
                        }
        
                        &[data-star="3"] {
                            background-position: 0 -440px;
                        }
        
                        &[data-star="3.5"] {
                            background-position: 0 -420px;
                        }
        
                        &[data-star="4"] {
                            background-position: 0 -480px;
                        }
        
                        &[data-star="4.5"] {
                            background-position: 0 -460px;
                        }
        
                        &[data-star="5"] {
                            background-position: 0 -500px;
                        }
        
        
                    }
                    &__view
                    {
                        span
                        {
                            color: #666;
                            font-size: 12px;

                        }

                    }
                }
                &__desc
                {
                    span
                    {
                     
                        color: #333;
                        font-weight: 500;
                        font-size: 14px;
                        &.desc
                        {
                            font-weight: 200;

                        }
                        a
                            {
                                color: #0073bb;
                                &:hover
                                {
                                    text-decoration: underline;

                                }

                            }
                    }
                }
            }
         
        }
        &__close
        {
            position: absolute;
            left: 10px;
            top:-25px;
            svg
            {
                fill:#999;;

            }
            span
            {
                color: #999;
            }
            &:hover
            {
                svg
                {
                    fill:#000;;
        
                }
                span
                {
                    color: #000;
                }
            }
        } 
        &__image
        {
        
            background-color: #000;
            display: flex;
            justify-content: center;
            text-align: center;
            align-items: center;
            height: 100%;
            a
            {
                img
                {

                }
            }
        }
        &__footer-slide
        {
            background: rgba(0,0,0,.7);
            height: 30px;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            display: flex;
            padding: 2px 0;
            &__left
            {
                margin-right: auto;
                margin-left: 6px;
                a
                {
                    color: #999;
                    svg
                    {
                        margin-left: 3px;
                        fill:#999;;

                    }
                
                    &:hover
                    {
                        color: #fff;
                        svg
                        {
                            fill: #fff;
                        }
                    
                    }
                }
            }
            &__right
            {
                margin-right: 6px;
                a
                {
                    color: #999;
                    svg
                    {
                        margin-left: 3px;
                        fill:#999;

                    }
                
                    &:hover
                    {
                        color: #fff;
                        svg
                        {
                            fill: #fff;
                        }
                    
                    }
                }
            }
        }
        .modal-content
        {
            border: none;
            border-radius: 6px;
        }
        .modal-content
        {
            border: none;
            border-radius: 6px;
        }
        .modal-body
        {
            padding: 0;
            border-radius: 6px;
        }
        .modal-dialog 
        {
            max-width:1300px;
            padding: 0 15px;
        }
        .swiper-container
        {
            height: 90vh;
        }
    
        .swiper-button-next
        {
            &::after
            {
                width: 48px;
                height: 48px;
                color: #fff;
            }
        }
        .swiper-button-prev
        {
            &::after
            {
                width: 48px;
                height: 48px;
                color: #fff;
            }
        }
        .swiper-pagination-fraction
        {
            color: #999;
            bottom: 3px;
        }
    }
}

@media(max-width:768px)
{
    .all-photos-content
    {
        &__wrapper
        {
            &__tabs
            {
                &__list
                {
                    ul
                    {
                        li
                        {
                            font-size: 14px;
                            padding: 6px 6px;
                        }
                    }
                }
            }
        }

    }
}
@media(max-width:576px)
{
    .all-photos-content
    {
        &__wrapper
        {
            &__title
            {
                h1
                {
                    font-size:25px;
                }
            }
            &__tabs
            {
                flex-direction: column;
                &__list
                {
                    order: 2;
                    flex: 0 0 100%;
                    width: 100%;

                }
                &__search
                {
                    margin-bottom:12px;
                    order:1;
                    flex: 0 0 100%;
                    width: 100%;
                }
            }
        }
        &__modal
        {
             &__content
             {
                 padding: 12px;
             }



        }
    }
}

@media(max-width:480px)
{
    .all-photos-content
    {
        &__wrapper
        {
            &__title
            {
                h1
                {
                    font-size:18px;
                }
            }
            &__gallery
            {
                ul
                {
                    margin:0 0;
                    li
                    {
                        width:calc(50% - 12px);
                        flex: 0 0 calc(50% - 12px);
                    }
                }
            }
            &__profile
            {
                flex-direction: column;
                
                &__add
                {
                    margin-right: 0;
                    margin-top: 12px;
                }
            }
           
        }
    }
}